iotgatewayhub.factory('workerProcessRestService', ['$http', '$uibModal', '$rootScope', function($http, $uibModal, $rootScope){
    var workerProcessRestService = {};

    function createWorkID() {
        var randomNum = Math.random();
        return randomNum.toString();
    }

    function checkPeriodicCheckingResponseError(response) {
        var report = false;
        var return_message = '';

        if ((response.status === -1) || (response.status === 408) || (response.status === 401)) {
            report = checkResponseErrorReport();
            if (report) {
                if (response.status === -1) {
                    return_message = 'Cannot connect to the gateway server anymore!';
                } else if (response.status === 408) {
                    return_message = 'The request got timeout error: http status is 408!';
                } else if (response.status === 401) {
                    return_message = 'The request got access denied: http status is 401!';
                } else {
                    return_message = '';
                }
            } else {
                return_message = '';
            }
        } else {
            return_message = 'Something went wrong: HTTP response status is ' + response.status;
        }

        // empty string means : do not report
        return return_message;
    }

    function informGeneralError(titleMessage, descMessage, barBeforeMessage) {
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.general.checking.inprogress.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope){
                $scope.title_message = titleMessage;
                $scope.description_message = descMessage;
                $scope.bar_before_message = barBeforeMessage;
                $scope.hasError = true;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    }

    workerProcessRestService.informGeneralChecking = function(httpMethod, httpURL, noParameter, httpJSON, httpBody, titleMessage, descMessage, barBeforeMessage, realSuccessCallback, checkPeriod) {
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.general.checking.inprogress.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope){
                console.log("hub controller: API call " + httpMethod + " " + httpURL);

                $scope.title_message = titleMessage;
                $scope.description_message = descMessage;
                $scope.bar_before_message = barBeforeMessage;
                $scope.hasError = false;

                var workID = createWorkID();

                var http_call = {
                    method: httpMethod
                };

                if (httpJSON === true) {
                    // id and is_checking are inside the request body
                    httpBody.id = workID;
                    httpBody.is_checking = 'False';
                    http_call.url = httpURL;
                    http_call.headers = {'Content-type' : 'application/json'};
                    http_call.data = JSON.stringify(httpBody);
                } else {
                    // id and is_checking are in the url directly
                    if (httpMethod === 'GET') {
                        if (noParameter === true) {
                           http_call.url = httpURL + '?is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                        } else {
                            http_call.url = httpURL + '&is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                        }
                    } else {
                        if (noParameter === true) {
                            http_call.url = httpURL + '?is_checking=False&id=' + workID;
                        } else {
                            http_call.url = httpURL + '&is_checking=False&id=' + workID;
                        }
                    }
                }

                function periodicChecking() {
                    var http_call = {
                        method: httpMethod
                    };

                    if (httpJSON === true) {
                        // id and is_checking are inside the request body
                        httpBody.id = workID;
                        httpBody.is_checking = 'True';
                        http_call.url = httpURL;
                        http_call.headers = {'Content-type' : 'application/json'};
                        http_call.data = JSON.stringify(httpBody);
                    } else {
                        // id and is_checking are in the url directly
                        if (httpMethod === 'GET') {
                            if (noParameter === true) {
                                http_call.url = httpURL + '?is_checking=True&id=' + workID + '&nocache=' + new Date().getTime();
                            } else {
                                http_call.url = httpURL + '&is_checking=True&id=' + workID + '&nocache=' + new Date().getTime();
                            }
                        } else {
                            if (noParameter === true) {
                                http_call.url = httpURL + '?is_checking=True&id=' + workID;
                            } else {
                                http_call.url = httpURL + '&is_checking=True&id=' + workID;
                            }
                        }
                    }

                    console.log("hub controller: API checking call " + httpMethod + " " + http_call.url);
                    $http(http_call).then(
                        function successCheckingCallback(response) {
                            if (response.data.status === 'success') {
                                console.log("hub controller: got work results");
                                setupPeriodicChecking(false);
                                realSuccessCallback(response.data);
                                modalInstance.dismiss('cancel');
                            } else if (response.data.status === 'untrusted') {
                                console.log("hub controller: got work results - untrusted");
                                setupPeriodicChecking(false);
                                realSuccessCallback(response.data);
                                modalInstance.dismiss('cancel');
                            } else {
                                if (response.data.in_progress === true) {
                                    // check again if needed.
                                    console.log("hub controller: cannot check work, still in progress");
                                    setupPeriodicChecking(true);
                                    setTimeout(periodicChecking, checkPeriod);
                                } else {
                                    console.log("hub controller: cannot check work, error");
                                    setupPeriodicChecking(false);
                                    informGeneralError(titleMessage, descMessage, "The operation failed! " + response.data.message);
                                    modalInstance.dismiss('cancel');
                                }
                            }
                        }, function errorCheckingCallback(response) {
                            console.log("hub controller: cannot check work, error");
                            var temp_error_message = checkPeriodicCheckingResponseError(response);
                            if (temp_error_message) {
                                setupPeriodicChecking(false);
                                informGeneralError(titleMessage, descMessage, temp_error_message);
                                modalInstance.dismiss('cancel');
                            } else {
                                // do not report, keep checking
                                setupPeriodicChecking(true);
                                setTimeout(periodicChecking, checkPeriod);
                            }
                        });
                }

                $http(http_call).then(
                    function successCallback(response) {
                        if (response.data.status === 'success') {

                            // start checking after some delay
                            setupPeriodicChecking(true);
                            setTimeout(periodicChecking, checkPeriod);

                            console.log("hub controller: successfully submitted work");
                        } else {
                            if (response.data.in_progress === true) {
                                console.log("hub controller: cannot submit work, other work in progress");
                                var error_message = response.data.work_type + ' (run by another user) is still in progress!';
                                informGeneralError(titleMessage, descMessage, error_message);
                                modalInstance.dismiss('cancel');
                            } else {
                                console.log("hub controller: cannot submit work, error");
                                informGeneralError(titleMessage, descMessage, "The operation failed! " + response.data.message);
                                modalInstance.dismiss('cancel');
                            }
                        }
                    },function errorCallback(response){
                        console.log("hub controller: cannot submit work, error");
                        informGeneralError(titleMessage, descMessage, "Something went wrong: HTTP status " + response.status);
                        modalInstance.dismiss('cancel');
                    });
            }
        });
    };

    workerProcessRestService.performGeneralExecution = function(httpMethod, httpURL, noParameter, httpJSON, httpBody, passedSuccessCallback, passedErrorCallback, checkPeriod) {
        var workID = createWorkID();

        var http_call = {
            method: httpMethod
        };

        if (httpJSON === true) {
            // id and is_checking are inside the request body
            httpBody.id = workID;
            httpBody.is_checking = 'False';
            http_call.url = httpURL;
            http_call.headers = {'Content-type' : 'application/json'};
            http_call.data = JSON.stringify(httpBody);
        } else {
            // id and is_checking are in the url directly
            if (httpMethod === 'GET') {
                if (noParameter === true) {
                    http_call.url = httpURL + '?is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                } else {
                    http_call.url = httpURL + '&is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                }
            } else {
                if (noParameter === true) {
                    http_call.url = httpURL + '?is_checking=False&id=' + workID;
                } else {
                    http_call.url = httpURL + '&is_checking=False&id=' + workID;
                }
            }
        }

        function periodicChecking() {
            var http_call = {
                method: httpMethod
            };

            if (httpJSON === true) {
                // id and is_checking are inside the request body
                httpBody.id = workID;
                httpBody.is_checking = 'True';
                http_call.url = httpURL;
                http_call.headers = {'Content-type' : 'application/json'};
                http_call.data = JSON.stringify(httpBody);
            } else {
                // id and is_checking are in the url directly
                if (httpMethod === 'GET') {
                    if (noParameter === true) {
                        http_call.url = httpURL + '?is_checking=True&id=' + workID + '&nocache=' + new Date().getTime();
                    } else {
                        http_call.url = httpURL + '&is_checking=True&id=' + workID + '&nocache=' + new Date().getTime();
                    }
                } else {
                    if (noParameter === true) {
                        http_call.url = httpURL + '?is_checking=True&id=' + workID;
                    } else {
                        http_call.url = httpURL + '&is_checking=True&id=' + workID;
                    }
                }
            }

            console.log("hub controller: API checking call " + httpMethod + " " + http_call.url);
            $http(http_call).then(
                function successCheckingCallback(response) {
                    if (response.data.status === 'success') {
                        console.log("hub controller: got work results");
                        setupPeriodicChecking(false);
                        passedSuccessCallback(response.data);
                    } else if (response.data.status === 'untrusted') {
                        console.log("hub controller: got work results - untrusted");
                        setupPeriodicChecking(false);
                        passedSuccessCallback(response.data);
                    } else {
                        if (response.data.in_progress === true) {
                            // check again if needed.
                            console.log("hub controller: cannot check work, still in progress");
                            setupPeriodicChecking(true);
                            setTimeout(periodicChecking, checkPeriod);
                        } else {
                            console.log("hub controller: cannot check work, error");
                            setupPeriodicChecking(false);
                            var error_message = response.data.message;
                            passedErrorCallback(error_message);
                        }
                    }
                }, function errorCheckingCallback(response) {
                    console.log("hub controller: cannot check work, error");
                    var temp_error_message = checkPeriodicCheckingResponseError(response);
                    if (temp_error_message) {
                        setupPeriodicChecking(false);
                        passedErrorCallback(temp_error_message);
                    } else {
                        // do not report, keep checking
                        setupPeriodicChecking(true);
                        setTimeout(periodicChecking, checkPeriod);
                    }
                });
        }

        $http(http_call).then(
            function successCallback(response) {
                if (response.data.status === 'success') {

                    // start checking after some delay
                    setupPeriodicChecking(true);
                    setTimeout(periodicChecking, checkPeriod);

                    console.log("hub controller: successfully submitted work");
                } else {
                    if (response.data.in_progress === true) {
                        console.log("hub controller: cannot submit work, other work in progress");
                        setupPeriodicChecking(false);
                        passedErrorCallback(response.data.work_type + ' (run by another user) is still in progress! Please try again later!');
                    } else {
                        console.log("hub controller: cannot submit work, error");
                        setupPeriodicChecking(false);
                        passedErrorCallback(response.data.message);
                    }
                }
            },function errorCallback(response){
                console.log("hub controller: cannot submit work, error");
                setupPeriodicChecking(false);
                passedErrorCallback("Something went wrong: HTTP status " + response.status);
            });
    };

    workerProcessRestService.performGeneralExecutionOnlySubmit = function(httpMethod, httpURL, noParameter, httpJSON, httpBody, passedSuccessCallback, passedErrorCallback) {
        var workID = createWorkID();

        var http_call = {
            method: httpMethod
        };

        if (httpJSON === true) {
            // id and is_checking are inside the request body
            httpBody.id = workID;
            httpBody.is_checking = 'False';
            http_call.url = httpURL;
            http_call.headers = {'Content-type' : 'application/json'};
            http_call.data = JSON.stringify(httpBody);
        } else {
            // id and is_checking are in the url directly
            if (httpMethod === 'GET') {
                if (noParameter === true) {
                    http_call.url = httpURL + '?is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                } else {
                    http_call.url = httpURL + '&is_checking=False&id=' + workID + '&nocache=' + new Date().getTime();
                }
            } else {
                if (noParameter === true) {
                    http_call.url = httpURL + '?is_checking=False&id=' + workID;
                } else {
                    http_call.url = httpURL + '&is_checking=False&id=' + workID;
                }
            }
        }

        $http(http_call).then(
            function successCallback(response) {
                if (response.data.status === 'success') {
                    setupPeriodicChecking(false);
                    passedSuccessCallback(response.data);
                    console.log("hub controller: successfully submitted work");
                } else {
                    if (response.data.in_progress === true) {
                        console.log("hub controller: cannot submit work, other work in progress");
                        setupPeriodicChecking(false);
                        passedErrorCallback(response.data.work_type + ' (run by another user) is still in progress! Please try again later!');
                    } else {
                        console.log("hub controller: cannot submit work, error");
                        setupPeriodicChecking(false);
                        passedErrorCallback(response.data.message);
                    }
                }
            },function errorCallback(response){
                console.log("hub controller: cannot submit work, error");
                setupPeriodicChecking(false);
                passedErrorCallback("Something went wrong: HTTP status " + response.status);
            });
    };

    return workerProcessRestService;
}]);
