iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('hub.nonetwork',{ 
        url:         '/hub/nonetwork', 
        templateUrl: '/partials/hub.nonetwork.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope){
            if(!authorization.username){
                $state.go('welcome.login',{});
            }

            $scope.configureNetwork = function(){
                $scope.getProxySettings(function() {
                    $state.go('hub.networksettings',{});
                });
            };

            $scope.skipConfigureNetwork = function(){
                $rootScope.loggedin = true;
                $state.go('hub.dashboard.sensors',{}); 
            };
        }
    });
});