iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    $stateProvider.state('hub.license',{ 
        url:         '/hub/license',
        templateUrl: '/partials/hub.license.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,utilityFunctionsService){

            // If we are here, then oem_branding_cookies_names's length is > 0
            var eula_index  = -1;
            $scope.eulaLoaded = false;

            function findUnacceptedCookieIndex() {
                var temp_index = -1;
                for (i = 0; i < oem_branding_cookies_names.length; i++) {
                    if (!getCookie(oem_branding_cookies_names[i])) {
                        temp_index = i;
                        all_accepted = false;
                        break;
                    }
                }
                return temp_index;
            }

            function checkEulaCookies(doApply) {
                eula_index = findUnacceptedCookieIndex();
                if (eula_index === -1) {
                    // all accepted
                    $rootScope.accepted_license = true;
                    $state.go("hub.networkcheck");
                } else {
                    // still has not accepted one
                    $scope.eulaLoaded = false;
                    setTimeout(function() {
                        $scope.current_eula_html_file = oem_branding_eulas[eula_index];
                        $scope.current_eula_cookie_name = oem_branding_cookies_names[eula_index];
                        console.log("###########current eula##############");
                        console.log($scope.current_eula_html_file);
                        console.log($scope.current_eula_cookie_name);
                        if (doApply) {
                            $scope.$apply();
                        }
                    }, 1500);
                }
            }

            $scope.cancel = function(){
                authorization = {};
                $rootScope.loggedin =  false;
                $state.go('welcome.login',{});
            };

            $scope.agree = function(){
                // set this eula as accepted
                utilityFunctionsService.setCookie($scope.current_eula_cookie_name,"true",license_duration_days);

                // continue checking
                checkEulaCookies(true);
            };

            if(!authorization.username){
                console.log("auth failed in hub license 001b");
                $state.go('welcome.login',{});
            }

            // initial checking
            checkEulaCookies(true);
        }
    });
});