iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('welcome',{ //only state that is not a descendant of hub. this presents the login page and background image
        url: '', 
        abstract:    true,
        templateUrl: '/partials/welcome.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,$uibModal){
            setupNoConnection(false);

            $rootScope.loggedin = false;
            $scope.authentication = {};
            $scope.connected = false;

            $scope.broadcastFailure = function(bMessage) {
                $scope.$broadcast(bMessage);
            };

            $scope.handleAuthenticated = function() {
                authorization = {"username": $scope.authentication.username};
                $rootScope.loggedin    = true;

                //so we have to save these in scope to check against upgrade to pro.
                //don't quite like this idea security-wise but it is only way to pre-check auth before sending call,
                //if that is what they want.
                $rootScope.credentials = {"username": $scope.authentication.username, "password": $scope.authentication.password};

                if (!$rootScope.accepted_license) {
                    $state.go('hub.license',{});
                } else {
                    $state.go('hub.networkcheck',{});
                }
            };

            $scope.checkAuth = function(){
                var _datatype = "json";
                var _method   = "POST";
                var _uri      = '/api/validate_session';

                $http({
                    method: _method,
                    url: _uri,
                    data: JSON.stringify(),
                    headers: {
                        'Content-type' : 'application/json'
                    }
                }).then(function successCallback(response){
                    if(response.data.status==="success"){
                        authorization = {"username": "preauthed"};
                        $rootScope.loggedin = true;

                        if (!$rootScope.accepted_license) {
                            $state.go('hub.license',{});
                        } else {
                            $state.go('hub.networkcheck',{});
                        }
                    } else {
                        // should not be reached
                        $scope.showLogin = true;
                    }
                }, function errorCallback(response){
                    $scope.showLogin = true;
                });
            };

            $scope.continueWelcomeLogin = function() {
                if (do_validate_session) {
                    $scope.checkAuth();
                } else {
                    // force to show login
                    $scope.showLogin = true;
                }
                do_validate_session = true;
            };

            $scope.setOEMBrandingCookiesNames = function() {
                oem_branding_cookies_names.length = 0;
                for (i = 0; i < oem_branding_eulas.length; i++) {
                    var temp_datetime = '';
                    if (i < oem_branding_eulas_datetime.length) {
                        temp_datetime = oem_branding_eulas_datetime[i];
                    }
                    var temp_cookiename = oem_branding_eulas[i].concat(temp_datetime);
                    // \s is regex for whitespace. g is global flag.
                    // temp_cookiename = temp_cookiename.replace(/\s+/g, '');
                    // keep only a-Z and 0 - 9
                    temp_cookiename = temp_cookiename.toLowerCase().replace(/[^a-zA-Z0-9]+/g, '');
                    oem_branding_cookies_names.push(temp_cookiename);
                }
                console.log("#############oem cookies names#################");
                console.log(oem_branding_cookies_names);
            };

            $scope.checkOEMBrandingCookies = function() {
                var temp_accepted = true;
                for (i = 0; i < oem_branding_cookies_names.length; i++) {
                    if (!getCookie(oem_branding_cookies_names[i])) {
                        temp_accepted = false;
                        break;
                    }
                }
                $rootScope.accepted_license = temp_accepted;
                console.log("accepted all licenses? "+$rootScope.accepted_license);
            };

            $scope.getOEMBranding = function(){
                $http({
                    method: 'GET',
                    url:    '/api/oembranding?nocache=' + new Date().getTime()
                }).then(function successCallback(response) {
                    console.log("got oem branding");

                    try {
                        if (response.data.status === 'success') {
                            // set the oem branding config
                            oem_branding_logo_file = response.data.result.logo_file;
                            oem_branding_eulas = response.data.result.eula_files;
                            oem_branding_eulas_datetime = response.data.result.eula_files_datetime;
                            $scope.setOEMBrandingCookiesNames();
                            $scope.checkOEMBrandingCookies();
                        }
                    } catch(err){
                    }

                    $scope.continueWelcomeLogin();
                },function errorCallback(response) {
                    console.log("error getting oem branding");
                    console.log(response);
                    $scope.continueWelcomeLogin();
                });
            };

            $scope.getOEMBranding();
        }
    });
});
