iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('hub.dashboard',{
        url: '',
        abstract: true,
        templateUrl: '/partials/hub.dashboard.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope,$timeout){

            $scope.timerExpired = function(chartMatch) {
                console.log("timerExpired", chartMatch);
                chartMatch.connected = false;
                $scope.$apply();
            };
            
            $rootScope.isSensorPanelExpanded = true; // determines state of top bar: expanded or minimized, utilized by dashboardToggle()
          
            $scope.sensors_charted = []; // Container for sensors. Sensors have a charts property that contain each chart
            $scope.charts_flat_list = []; // Flat list of all charts, regardless of sensor parent

            $scope.dashboardToggle = function(){
                $rootScope.isSensorPanelExpanded = !$rootScope.isSensorPanelExpanded;
                // console.log("dashboardToggle: " + $rootScope.isSensorPanelExpanded);
            };

            $scope.carouselPrevious = function(){
                console.log("carousel previous");
            };

            $scope.carouselNext = function(){
                console.log("carousel next");
            };

            $scope.setDashboard();

            // called when the client loses its connection
            $scope.onConnectionLost = function(responseObject) {
                if (responseObject.errorCode !== 0) {
                    console.log("onConnectionLost:"+responseObject.errorMessage);
                }
            };

            // called when a message arrives
            $scope.onMessageArrived = function(message) {
                //console.log("sensor data received");
                if(log_message_json){
                     console.log("onMessageArrived:"+message.payloadString);
                }
               
                try{
                    var entry = JSON.parse(message.payloadString);

                    var  _id = entry.id;
                    
                    // New chart - sensor data model for the visualizers
                    var sensorMatches = $.grep($scope.sensors_charted, function(e){ return e.id == _id; });
                    var sensorMatch;
                    if (sensorMatches.length > 1) {
                        console.log("Error in state.hub.dashboard:: A sensor was added twice to the same array", _id);
                    } else {
                        if (sensorMatches.length == 1) {
                            // One match, let's now consider if there is already a chart added to it
                            sensorMatch = sensorMatches[0];
                        } else {
                            // Zero matches. This sensor has not yet been added to the sensors list
                            sensorMatch = { 
                            charts: [],
                            id: _id, 
                            title: entry.title
                            };
                            $scope.sensors_charted.push(sensorMatch);
                        }
                        sensorMatch.mostRecentValue = entry.datum[1];
                        sensorMatch.priority = parseInt(entry.priority);
                        sensorMatch.mostRecentUnits = entry.units;
                        sensorMatch.mostRecentSource = entry.dataSource;
                        sensorMatch.mostRecentTimestamp = new Date(parseInt(entry.datum[0])); 
                    }

                    var chartMatches = $.grep(sensorMatch.charts, function(e){ return e.dataSource == entry.dataSource; });
                    var chartMatch;
                    if (chartMatches.length > 1) {
                        console.log("Error in state.hub.dashboard:: A chart was added twice to the same sensor", _id, entry.dataSource);
                    } else if (chartMatches.length == 1) {
                        chartMatch = chartMatches[0];
                    } else {
                        // Zero matches. This chart has not yet been added to the sensor's chart list
                        chartMatch = {};
                        sensorMatch.charts.push(chartMatch);
                        $scope.charts_flat_list.push(chartMatch);
                    }
                    
                    chartMatch.parentSensorTitle = sensorMatch.title;
                    chartMatch.chartType = entry.chartType;
                    if (entry.chartType == "line") {
                        chartMatch.value = { x: entry.datum[0], y: entry.datum[1]};
                    } else {
                        chartMatch.value = entry.datum[1];
                    }
                    chartMatch.mostRecentValue = entry.datum[1];
                    chartMatch.sourcePriority = parseInt(entry.sourcePriority);
                    chartMatch.dataSource = entry.dataSource;
                    chartMatch.min = entry.min;
                    chartMatch.max = entry.max;  
                    chartMatch.stepWidth = (entry.max - entry.min) / 5;
                    chartMatch.pointsToShow = parseInt(entry.points);     
                    chartMatch.units = entry.units;   
                    chartMatch.timeToLive = parseFloat(entry.ttl);
                    chartMatch.connected = true;
                    chartMatch.mostRecentTimestamp = new Date(parseInt(entry.datum[0])); 
                    if (chartMatch.timerExpiredPromise) $timeout.cancel(chartMatch.timerExpiredPromise);
                    chartMatch.timerExpiredPromise = $timeout( function(){ $scope.timerExpired(chartMatch); }, chartMatch.timeToLive * 1000);  
                    $scope.$apply();

                }catch(err){
                    //console.log("ERROR onMessageArrive "+err);
                }
            };

            $scope.mqttInit = function(){
                console.log("hub dashboard mqttInit 150a");
                //MQTT
                /**
                var broker = {
                  host: config.broker.host,
                  port: config.broker.wssPort,    //default port is  WSS port:9001
                  id: "client_" + parseInt(Math.random() * 100, 10),
                  topic: config.broker.topic,
                  reconnectTimeout: config.broker.reconnectTimeout,
                  reconnectInterval: config.broker.reconnectInterval,
                  protocol: window.location.protocol,
                  path: "/mqtt"
                };
                **/
                var loop = 0;
                var reconnect = false; //this is how Rob had it configured. Maybe set this to true for production?

                //var _host = window.location.hostname;
                var _host = $window.config_sensor.broker.host !== null ? $window.config_sensor.broker.host : window.location.hostname;//$scope.device["lan"]["eth0"];
                if(no_local_config_hosts && (no_local_config_hosts.indexOf(window.location.hostname)!=-1)){
                    _host = window.location.hostname;
                }
                var _useSSL = false;
                var _port   = $window.config_sensor.broker.wsPort;
                if(window.location.protocol==='https:'){
                    _port = $window.config_sensor.broker.wssPort;
                    _useSSL = true;
                }

                var _id  =  "client_" + parseInt(Math.random() * 100, 10);
                var _reconnectInterval = $window.config_sensor.broker.reconnectInterval;
                var _reconnectTimeout  = $window.config_sensor.broker.reconnectTimeout;
                var _topic             = $window.config_sensor.broker.topic;
                $scope.mqtt_client = new $window.Paho.MQTT.Client(_host, _port, _id);
                $scope.mqtt_client.onConnectionLost = $scope.onConnectionLost;  
                $scope.mqtt_client.onMessageArrived = $scope.onMessageArrived;

                // connect the client
                $scope.mqtt_client.connect({
                    useSSL: _useSSL,
                    timeout: 3,
                    cleanSession: true,
                    onSuccess: function () {
                        console.log("hub dashboard MQTT connection succeeded to "+_host+":"+_port+" Subscribing to topic: "+_topic);
                        //$link.removeClass("disconnected").addClass("connected");                     
                        $scope.mqtt_client.subscribe(_topic,{qos: 1});
                    },
                    onFailure: function (message) {

                        function initMqtt() {$scope.mqttInit();  }

                      if(reconnect) {
                        console.log("MQTT connection failed. Retrying. Error Message -  "+ message.errorMessage);

                        while(loop < _reconnectInterval) {
                              loop++;
                                  setTimeout(initMqtt, _reconnectTimeout);
                        } 
                        reconnect = false;
                      }
                      else {
                        //$link.removeClass("connected").addClass("disconnected");
                        console.log("MQTT connection failed. Please ensure the message broker is up. Error Message -  "+ message.errorMessage);
                      }
                    }
                });
            };

            $scope.mqttInit();
        }
    });
});