iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    $stateProvider.state('hub.dashboard.sensors',{
        url: '/dashboard/sensors',
        templateUrl: '/partials/hub.dashboard.sensors.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope,$uibModal,utilityFunctionsService){
            $scope.sortType = 'parentSensorTitle';

            $scope.doManageSensors = function(){
                var _cookie     = getCookie("noreshow");
                console.log("hub control found noreshow cookie as "+_cookie+" #8910a");
                if(_cookie){
                    $scope.launchQuickTools('nodered');
                }else{
                    $scope.openManageSensorsDialogModal();
                }
            };

            $scope.openManageSensorsDialogModal = function(){

                console.log("hub control opening MANAGE SENSORS dialog modal #073a");
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.manage.sensors.dialog.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope,$state,$http,$rootScope){
                        $scope.noreshow = true;

                        $scope.closeModal = function(){
                            if($scope.noreshow){
                                console.log("hub control setting noreshow cookie true #8910");
                                utilityFunctionsService.setCookie("noreshow",true,license_duration_days);
                            }
                            console.log("hub control closing manage sensors dialog modal 0 #073c");
                            modalInstance.dismiss('cancel');
                            $scope.launchQuickTools('nodered');
                        };
                        $scope.viewTutorial = function(){
                            //console.log("continuing to sensors tutorial #073dx");
                            //modalInstance.dismiss('cancel');
                            //$state.go("hub.dashboard.tutorial.sensor");
                            if($scope.noreshow){
                                console.log("hub control setting noreshow cookie true #8910");
                                utilityFunctionsService.setCookie("noreshow",true,license_duration_days);
                            }
                            var url = $state.href('tutorial.sensorsetup', {parameter: "parameter"});
                            window.open(url,'_blank');
                        };
                        $scope.viewVideo = function(){
                            if($scope.noreshow){
                                console.log("hub control setting noreshow cookie true #8910");
                                utilityFunctionsService.setCookie("noreshow",true,license_duration_days);
                            }
                            var url = "https://software.intel.com/videos/iot-dev-hub-setup-sensor-in-node-red";
                            window.open(url,'_blank');
                        };

                    }

                });

            };

        }
    });
});