 iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('hub.networkcheck',{ 
        url:         '/hub/networkcheck',
        //template:"<div>&nbsp;</div>",
        templateUrl:    'partials/hub.loading.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope){
            if(!authorization.username){
                $state.go('welcome.login',{});
            }

            setupNoConnection(true);
            $scope.loadMessage = "Checking network connection, and if needed, adding repositories and building packages list. This may take a few minutes.";
            $scope.showMessage = true;

            // run this later so that GUI has a chance to refresh
            setTimeout(function() {
                    $scope.proceedToNetworkCheck();
                }, 1000);
        }
    });
 });
