iotgatewayhub.factory('utilityFunctionsService', ['$http', function($http){
    var utilityFunctionsService = {};

    utilityFunctionsService.isNumber = function(n) {
        return !isNaN(parseFloat(n)) && isFinite(n);
    };

    utilityFunctionsService.pad = function(n, width, z) {
        z = z || '0';
        n = n + '';
        return n.length >= width ? n : new Array(width - n.length + 1).join(z) + n;
    };

    utilityFunctionsService.parse_digest = function(_header) {
        var ws = '(?:(?:\\r\\n)?[ \\t])+';
        var token = '(?:[\\x21\\x23-\\x27\\x2A\\x2B\\x2D\\x2E\\x30-\\x39\\x3F\\x41-\\x5A\\x5E-\\x7A\\x7C\\x7E]+)';
        var quotedString = '"(?:[\\x00-\\x0B\\x0D-\\x21\\x23-\\x5B\\\\x5D-\\x7F]|'+ws+'|\\\\[\\x00-\\x7F])*"';
        var tokenizer = RegExp(token+'(?:=(?:'+quotedString+'|'+token+'))?', 'g');
        var tokens = _header.match(tokenizer);
        return tokens;
    };

    utilityFunctionsService.unq = function(quotedString) {
        return quotedString.substr(1, quotedString.length-2).replace(/(?:(?:\r\n)?[ \t])+/g, " ");
    };

    utilityFunctionsService.setCookie = function(cname, cvalue, exdays) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays*24*60*60*1000));
        var expires = "expires="+d.toUTCString();
        document.cookie = cname + "=" + cvalue + "; " + expires;
    };

    utilityFunctionsService.checkNetwork = function(_successCallback,_errorCallback){
        $http({
            method: 'GET',
            url:    '/api/proxy?request=test&nocache=' + new Date().getTime(),
            cache:  false,
        }).then(_successCallback,_errorCallback);
    };

    return utilityFunctionsService;
}]);
