// Used in HTML as hub-dashboard-radial-graph
iotgatewayhub.directive('hubDashboardRadialGraph', function () {
    return {
        restrict: 'E',
        templateUrl: 'partials/hub.dashboard.radialgraph.ng.html?version=' + gui_version,
        replace: true,
        scope: {
              data: '=',
              units: '=',
              upperRange: '=',
              lowerRange: '=',
              connected: '=',
        },
        link: function ($scope, $elem, attrs) {
            console.log($scope.scaleSteps);

            function createDataPair() {
                $scope.dataPair = [($scope.upperRange-$scope.lowerRange)-$scope.data, $scope.data];
            }

            $scope.$watch('data', function() {
                createDataPair();
            });

            $scope.options = {
                showTooltips: false,

                //Boolean - Whether we should show a stroke on each segment
                segmentShowStroke : true,

                //String - The colour of each segment stroke
                segmentStrokeColor : "#003c71",

                //Number - The width of each segment stroke
                segmentStrokeWidth : 4,

                //Number - The percentage of the chart that we cut out of the middle
                percentageInnerCutout : 80, // This is 0 for Pie charts

                //Number - Amount of animation steps
                animationSteps : 10,

                //String - Animation easing effect
                animationEasing : "easeOutBounce",

                //Boolean - Whether we animate the rotation of the Doughnut
                animateRotate : false,

                //Boolean - Whether we animate scaling the Doughnut from the centre
                animateScale : false,
            };

            $scope.doughnutColors = ["#003c71","#8DC449"];
            $scope.labels = ["",""];

            $scope.$watch('connected', function() {
                if ($scope.connected) {
                    $scope.doughnutColors = ["#003c71","#8DC449"];
                } else {
                    $scope.doughnutColors = ["#003c71","#7f868c"];
                }
            });
        }
    };
});