// Used in HTML as hub-loading
iotgatewayhub.directive('hubLoading', function () {
    return {
        restrict : 'E',
        templateUrl: 'partials/hub.loading.ng.html?version=' + gui_version,

        controller: function ($scope, $http) {
            $scope.loadMessage = "Loading..";
            $scope.showMessage = true; // for modern browsers
            console.log("hubLoading ", $scope);
        }
    };
});