// These are general functions used in several states.
iotgatewayhub.expandHub_pro_upgrade = function($scope,$http,$state,$uibModal,$timeout,workerProcessRestService,utilityFunctionsService) {

    $scope.checkProStatus  = function(){
        console.log("hub controller: checking pro status");
        $http({
            method: 'GET',
            url:    '/api/pro?nocache=' + new Date().getTime() 
        }).then(function successCallback(response) {
            if (response.data.result === 'NA') {
                // This means that the backend cannot get the pro status, so we do not update status.
            } else {
                $scope.pro = (response.data.result === 'True') ;
            }
            console.log("hub controller: found pro as "+$scope.pro+" #7223c"+response.status);
        }, function errorCallback(response) {
            //this only happens for server error
            console.log("errorCallback with HTTP status "+response.status);
        });
    };

    $scope.confirmOSUpgrade = function(callback){
        console.log("hub confirming os upgrade #9033a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.1.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.onSuppress = function(shouldSuppress) {
                    if(!shouldSuppress) return;
                    utilityFunctionsService.setCookie("upgradeMessage",false,license_duration_days);
                };

                $scope.closeModal = function(){
                    console.log("hub canceling os upgrade #9033b");
                    modalInstance.dismiss('cancel');
                };

                $scope.onNotNow = function(){
                    console.log("hub canceling os upgrade via 'Not now' #9033b");
                    modalInstance.dismiss('cancel');
                    if(angular.isFunction(callback))callback();
                };

                $scope.continueOSUpgrade = function(){
                    console.log("hub opening os upgrade 2 #9033c");
                    console.log("admin tools mec status "+$scope.mec.installed+" #0812a");
                    if($scope.mec.enabled){
                        $scope.openOSUpgradeModal1b();
                        modalInstance.dismiss('cancel');
                        return;
                    }
                    $scope.openOSUpgradeModal2();
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSUpgradeModal1b = function(){
        console.log("hub confirming os upgrade #9033a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.1b.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.closeModal = function(){
                    console.log("hub canceling os upgrade #9033b");
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSUpgradeModal2 = function(){
        console.log("hub opening os upgrade 2 #9034a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.2.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){

                $scope.credentials   = {username: null, password: null};
                $scope.error_message = null;

                $scope.closeModal = function(){
                    console.log("hub canceling os upgrade #9034b");
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSUpgrade = function(){
                    $scope.error_message = '';
                    $scope.openOSUpgradeModal3($scope.credentials.username,$scope.credentials.password);
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSUpgradeModal3 = function(_username,_password){
        console.log("hub opening os upgrade 3 #9035a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.3.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                if ($scope.isQuark === true) {
                    $scope.timeHours = 'up to one hour';
                } else {
                    $scope.timeHours = 'less than one hour';
                }

                $scope.cancelOSUpgrade = function(){
                    console.log("hub canceling os upgrade 3 #9035b");
                    modalInstance.dismiss('cancel');
                };

                data = {"username":_username,"password":_password};
                workerProcessRestService.performGeneralExecution("POST", "/api/pro", false, true, data,
                    function successCallback(responseData) {
                        $scope.checkMECDetails();
                        $scope.checkProStatus();
                        $scope.openOSUpgradeModal4();
                        modalInstance.dismiss('cancel');
                    }, function errorCallback(errorMessage) {
                        $scope.openOSUpgradeModal4E(errorMessage);
                        modalInstance.dismiss('cancel');
                    }, rest_check_interval_milliseconds_l);
            }

        });
    };

    $scope.openOSUpgradeModal4 = function(_username,_password){
        console.log("hub opening os upgrade 4 #9036a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.4.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){

                $scope.rebooting = false;
                $scope.rebooted  = false;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSUpgradeModal4E = function(_message){
        console.log("hub opening os upgrade 4E #9036E");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.os.upgrade.4E.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.error_message = _message;

                $scope.closeModal = function(){
                    console.log("hub clsoing os upgrade 4 #9036b");
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };
};
