// These are general functions used in several states.
iotgatewayhub.expandHub_openOSUpdate = function($scope,$uibModal,$http,workerProcessRestService) {

    ///////////////////////////////////////
    // GUI Modals
    //////////////////////////////////////

    $scope.enterOSCredential = function(){
        console.log("hub entering OS credential");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.confirm.os.update.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.do_enter_credential = true;
                $scope.do_confirm_update = false;

                $scope.credentials = {username: null, password: null};
                $scope.cancelOSUpdate = function(){
                    console.log("hub canceling OS credential");
                    $scope.informOSUpdate_CancelWarning();
                    modalInstance.dismiss('cancel');
                };

                $scope.continueChecking = function(){
                    $scope.checkOSUpdate($scope.credentials);
                    modalInstance.dismiss('cancel');
                };

            }

        });
    };

    $scope.checkOSUpdate = function(_credentials) {
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.inprogress.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                console.log("hub controller: getting osupdate 007a");

                $scope.title_message = "OS Update: Checking in Progress";
                $scope.description_message = "Please do not power-down the gateway. Will be auto-redirected to another page once checking is complete.";
                $scope.bar_before_message = "Checking update availability...";
                $scope.bar_after_message = null;
                $scope.error_message = null;
                $scope.show_close_button = false;

                // prepare for body data
                if (typeof _credentials === "undefined") {
                    content_body = {type: "check", request: "rcpl"};
                } else {
                    content_body = _credentials;
                    content_body.type = "check";
                    content_body.request = "rcpl";
                }

                // make the POST call
                tempURL = '/api/osup';
                workerProcessRestService.performGeneralExecution('POST', tempURL, false, true, content_body,
                    function successCallback(data) {
                        if (data.status === 'failure') {
                            $scope.informOSUpdateError(data.message);
                            modalInstance.dismiss('cancel');
                        } else {
                            $scope.foundOSUpdate = data.update;
                            if($scope.foundOSUpdate){
                                $scope.confirmOSUpdate(_credentials);
                                modalInstance.dismiss('cancel');
                            }else{
                                $scope.checkOSUpdate_Packages(modalInstance);
                            }
                        }
                    }, function errorCallback(errorMessage){
                        $scope.informOSUpdateError(errorMessage);
                        modalInstance.dismiss('cancel');
                    }, rest_check_interval_milliseconds);
            }
        });
    };

    $scope.checkOSUpdate_Packages = function(modalInstance) {
        console.log("hub controller: getting osupdate_package");

        // prepare for body data
        content_body = {type: "check", request: "package"};

        // make the POST call
        tempURL = '/api/osup';
        workerProcessRestService.performGeneralExecution('POST', tempURL, false, true, content_body,
            function successCallback(data) {
                if (data.status === 'failure') {
                    $scope.informOSUpdateError(data.message);
                    modalInstance.dismiss('cancel');
                } else {
                    $scope.foundOSUpdate_Packages = data.package_update;
                    if($scope.foundOSUpdate_Packages){
                        $scope.OSUpdate_Packages = data.packages;
                        $scope.confirmOSUpdatePackages();
                        modalInstance.dismiss('cancel');
                    }else{
                        $scope.informOSUpdateNotAvailable();
                        modalInstance.dismiss('cancel');
                    }
                }
            }, function errorCallback(errorMessage) {
                $scope.informOSUpdateError(errorMessage);
                modalInstance.dismiss('cancel');
            }, rest_check_interval_milliseconds);
    };

    $scope.confirmOSUpdate = function(_credentials){
        console.log("hub confirming os update");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.confirm.os.update.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.do_enter_credential = false;
                $scope.do_confirm_update = true;

                $scope.cancelOSUpdate = function(){
                    console.log("hub canceling os update");
                    $scope.informOSUpdate_CancelWarning();
                    modalInstance.dismiss('cancel');
                };

                $scope.doOSUpdate = function(){
                    $scope.showOSUpdate(_credentials);
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.confirmOSUpdatePackages = function(){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.confirm.os.update.package.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.cancelOSUpdate = function(){
                    $scope.informOSUpdate_CancelWarning();
                    modalInstance.dismiss('cancel');
                };

                $scope.doOSUpdate_Packages = function(){
                    $scope.showOSUpdatePackages();
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.showOSUpdate = function(_credentials){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.inprogress.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.title_message = "OS Update: Installation in Progress";
                if ($scope.isQuark === true) {
                    $scope.description_message = "Please do not power-down the gateway. The OS update is in progress, downloading updates and installing onto this gateway. The update takes several hours in most network conditions.";
                } else {
                    $scope.description_message = "Please do not power-down the gateway. The OS update is in progress, downloading updates and installing onto this gateway. The update takes up to an hour in most network conditions.";
                }
                $scope.bar_before_message = "";
                $scope.bar_after_message = "The gateway will automatically restart once restore is complete.";
                $scope.error_message = null;
                $scope.show_close_button = false;

                console.log("performing os update");

                $scope.performUpdateSequence = function(){
                    $scope.bar_before_message = 'Update in Progress...';
                    $scope.error_message  = null;

                    // prepare for body data
                    content_body = _credentials;
                    content_body.type = "update_rcpl";

                    // make the POST call
                    tempURL = '/api/osup';
                    workerProcessRestService.performGeneralExecution('POST', tempURL, false, true, content_body,
                        function successCallback(data) {
                            if (data.status === 'success'){
                                $scope.informOSUpdateRestart();
                                modalInstance.dismiss('cancel');
                            } else {
                                $scope.informOSUpdateError(data.message);
                                modalInstance.dismiss('cancel');
                            }
                        }, function errorCallback(errorMessage) {
                            $scope.informOSUpdateError(errorMessage);
                            modalInstance.dismiss('cancel');
                        }, rest_check_interval_milliseconds_ll);
                };

                $scope.performUpdateSequence();
            }
        });
    };

    $scope.showOSUpdatePackages = function(){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.inprogress.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.title_message = "OS Update: Installation in Progress";
                $scope.description_message = "Please do not power-down the gateway. The OS update is in progress, downloading updates and installing onto this gateway. The updating time depends on the number of packages that need to be updated.";
                $scope.bar_before_message = "Installing...";
                $scope.bar_after_message = null;
                $scope.error_message = null;
                $scope.show_close_button = false;

                console.log("hub controller: updating OS packages");

                // prepare for body data
                content_body = {type: "update_packages"};

                // make the POST call
                tempURL = '/api/osup';
                workerProcessRestService.performGeneralExecution('POST', tempURL, false, true, content_body,
                    function successCallback(data) {
                        if (data.status === 'success') {
                            // so that we can get the available update count to display.
                            try {
                                var p_list = JSON.parse(data.p_list);
                                $scope.setPackages(p_list);
                            } catch(e) {
                                // ignore error
                            }

                            $scope.informOSUpdateDone();
                            modalInstance.dismiss('cancel');
                        }else{
                            console.log("error updating OS packages.");
                            $scope.informOSUpdateError(response.data.message);
                            modalInstance.dismiss('cancel');
                        }
                    }, function errorCallback(errorMessage) {
                        $scope.informOSUpdateError(errorMessage);
                        modalInstance.dismiss('cancel');
                    }, rest_check_interval_milliseconds_l);
            }
        });
    };

    $scope.informOSUpdateRestart = function(){
        console.log("inform os update restart");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.end.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.title_message = "OS Update: Installation Complete";
                $scope.description_message = "";
                $scope.show_bar = true;
                $scope.bar_before_message = "Update Installed!";
                $scope.bar_after_message = "The gateway will automatically restart in 10 seconds.";
                $scope.show_last_error = false;
                $scope.show_last_normal = true;
                $scope.show_close = true;
                $scope.show_close_button = false;
                $scope.show_warning_html = false;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.rebootGateway(
                    function successCallback(response) {
                        console.log("rebooted gateway: " + response.data.status);
                        $timeout(function(){
                            modalInstance.dismiss('cancel');
                        }, restart_welcome_latency_milliseconds - 1000);
                    }, function errorCallback(response) {
                        console.log("gateway reboot failed");
                        $scope.bar_after_message = "Rebooting gateway failed! Please manually restart the gateway!";
                        $scope.show_last_error = true;
                        $scope.show_last_normal = false;
                    }, restart_welcome_latency_milliseconds);
            }
        });
    };

    $scope.informOSUpdateError = function(_error_message){
        console.log("inform os update error");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.end.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.title_message = "OS Update: Installation Error";
                $scope.description_message = "";
                $scope.show_bar = true;
                $scope.bar_before_message = "Update Failed!";
                $scope.bar_after_message = _error_message;
                $scope.show_last_error = true;
                $scope.show_last_normal = false;
                $scope.show_close = true;
                $scope.show_close_button = false;
                $scope.show_warning_html = false;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.informOSUpdateNotAvailable = function(){
        console.log("hub confirming os update #8490a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.end.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.title_message = "OS Update: No Update";
                $scope.description_message = "";
                $scope.show_bar = true;
                $scope.bar_before_message = "Update Not Available!";
                $scope.bar_after_message = "";
                $scope.show_last_error = false;
                $scope.show_last_normal = true;
                $scope.show_close = false;
                $scope.show_close_button = true;
                $scope.show_warning_html = false;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.informOSUpdateDone = function(){
        console.log("inform os update done");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.end.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.title_message = "OS Update: Installation Complete";
                $scope.description_message = "";
                $scope.show_bar = true;
                $scope.bar_before_message = "Update Installed!";
                $scope.bar_after_message = "";
                $scope.show_last_error = false;
                $scope.show_last_normal = true;
                $scope.show_close = false;
                $scope.show_close_button = true;
                $scope.show_warning_html = false;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };

    $scope.informOSUpdate_CancelWarning = function(){
        // disable this for now
        return;

        console.log("inform os update cancel warning");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.inform.os.update.end.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.title_message = "OS Update: Update Cancelled";
                $scope.description_message = '';
                $scope.show_bar = false;
                $scope.bar_before_message = "";
                $scope.bar_after_message = "";
                $scope.show_last_error = false;
                $scope.show_last_normal = false;
                $scope.show_close = false;
                $scope.show_close_button = true;
                $scope.show_warning_html = true;

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };
};