//////////////////////////////
// getCookie and acceptedPrivacy are used by .config, .run, controller.....
//     Didn't find a way to define them in any kind of service (factory, provider, ...) yet.
/////////////////////////////
function getCookie(cname) {
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for (var i=0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0)==' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) === 0) {
            return c.substring(name.length,c.length);
        }
    }
    return "";
}

function acceptedPrivacy(){
    return getCookie('privacy');
}

function authenticate($q, $rootScope, $state, $timeout) {
    if(bypass_auth){
        return true;
    }
    if ($rootScope.loggedin) {
        // Resolve the promise successfully
        return $q.when();
    } else {
        // The next bit of code is asynchronously tricky.
        $timeout(function() {
            // This code runs after the authentication promise has been rejected.
            // Go to the log-in page
            console.log("unauthenticated request: routing to welcome page");
            $state.go('welcome.login');
        });
        return $q.reject();
    }
}

////////////////////////
// The following are for the case when the web response has status = -1 (no connection to gateway server), status = 408 (request timeout), or 401 (access denied)
//      If we are in periodic checking to poll for work results, we will give it some grace period.
///////////////////////
function setupNoConnection(start) {
    if (start) {
        // make the initial time way in the future... 10 days in the future
        response_target_error_start_time = new Date();
        response_target_error_start_time.setDate(response_target_error_start_time.getDate() + 10);

        start_trigger_no_connection = true;
        started_periodic_checking = false;
    } else {
        start_trigger_no_connection = false;
        started_periodic_checking = false;
    }
}

function setupPeriodicChecking(start) {
    started_periodic_checking = start;
}

function setResponseErrorTime(has_error) {
    if (has_error) {
        var currentTime = new Date();
        var diffTime = currentTime.getTime() - response_target_error_start_time.getTime();
        if (diffTime < 0) {
            // This is the first error.
            response_target_error_start_time = new Date();
        } else {
            // This is not the first error. Do nothing.
        }
    } else {
        // make the initial time way in the future... 10 days in the future
        response_target_error_start_time = new Date();
        response_target_error_start_time.setDate(response_target_error_start_time.getDate() + 10);
    }
}

function checkResponseErrorReport() {
    var currentTime = new Date();
    var diffTime = currentTime.getTime() - response_target_error_start_time.getTime();
    var diffTimeInSeconds = diffTime / 1000;

    if (started_periodic_checking) {
        // This is in periodic checking to pull work result, we give it some grace period (3 min).
        if (diffTimeInSeconds >= (60 * 3)) {
            return true; // report yes error
        } else {
            return false; // report no error
        }
    } else {
        // If this is not periodic checking to pull work result, report immediately
        return true; // report yes error
    }
}


////////////////////////
//  The main module
///////////////////////

var iotgatewayhub = angular.module("iotgatewayhub",['ui.router','ui.bootstrap','ngPasswordStrength', 'chart.js', 'perfect_scrollbar', 'timer'])
.config(function ($stateProvider, $urlRouterProvider, $httpProvider) {
    var accepted_privacy = acceptedPrivacy();
    if (accepted_privacy) {
        $urlRouterProvider.otherwise("/welcome/login");
    } else {
        $urlRouterProvider.otherwise("/welcome/privacy"); //default is to redirect to login page
    }

    // provider needs to be inject into .config, not .run.
    // This is global for all $http.
    // httpRequestsInterceptor is a factory service.
    $httpProvider.interceptors.push('httpRequestsInterceptor');
})
.factory('mqttClient',[function(){
    var client =  {};
    client.connect = function(){
        console.log("CONNECTING");
    };
    var factory = {};
    factory.get = function(){
        return client;
    };
    return factory;
}])
.run(function ($rootScope, $state, $location, $window, $uibModal, httpRequestsService) {
    console.log('Intel IoT Gateway Development Hub');

    function resolveFromUrl(url) {
        var states = $state.get();
        for(var i = 0, len = states.length; i< len; i++ ){
            if(states[i] && states[i].url === url){
                console.log("statename:"+states[i].name);
                return states[i].name;
            }
        }
    }

    var accepted_privacy = acceptedPrivacy();
    if (accepted_privacy) {
        if( requested_url && requested_url.indexOf('dashboard') > -1){
            $rootScope.redirectState = resolveFromUrl(requested_url);
        }
    } else {
        $location.path('/welcome/privacy');
    }

    $rootScope.$on('$locationChangeStart', function(event) {
        if(!acceptedPrivacy()){
            //event.preventDefault();
            $location.path('/welcome/privacy');
        }
    });


    $rootScope.$on("Gateway_Not_Reachable", function (event) {
        console.log("Get event: Gateway_Not_Reachable!");

        // Abort all requests
        httpRequestsService.abortAllRequests();

        var no_connection_template =
            '<div class="admin-step">' +
                '<div class="row">' +
                    '<div class="col-md-12">' +
                        '<h1>Intel IoT Gateway Developer Hub cannot reach the gateway!</h1>' +
                    '</div>' +
                '</div>' +
                '<div class="row">' +
                    '<div class="col-md-12">' +
                        '<div class="description">Please check gateway status, network connection, and then refresh or try again later. Warning: Your previous task may still be running on the gateway, and IoT Gateway Developer Hub may not reflect its running status.</div>' +
                    '</div>' +
                '</div><!-- .row -->' +
                '<div class="row admin-modal-btn-row">' +
                    '<div class="col-md-12">' +
                        '<div class="row">' +
                            '<div class="blue-button-md admin-modal-btn center-button" ng-click="clickRefresh()">Refresh</div>' +
                        '</div>' +
                    '</div>' +
                '</div><!-- .row -->' +
            '</div>';

        var modalInstance = $uibModal.open({
            template: no_connection_template, // for offline, cannot use templateURL.
            animation: true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function ($scope, $window) {
                $scope.clickRefresh = function () {
                    modalInstance.dismiss('cancel');
                    $window.location.reload(); // refresh the current page.

                    //$window.location.href = '/'; // this is for Firefox to work.

                    // For Firefox,  $location.path or $window.location.href will not be done before $window.location.reload is run.
                    // Therefore, $window.location.reload will reload the current URL.
                    //$window.location.reload();
                };
            }
        });

        /* window alert is blocking the whole web browser (for all tabs).... This is not ideal.
        $window.alert('Developer Hub cannot reach gateway!\n\nPlease check gateway status, network connection, and then refresh or try again later.\n\nWarning: The task you arer running may still be running on the gateway. However, Developer Hub may not be able to sync up to that status if the task is still running.');
        */
    });
});
