iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('welcome.privacy',{
        url:         '/welcome/privacy', 
        templateUrl: '/partials/welcome.privacy.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,$uibModal,utilityFunctionsService){
             console.log("welcome privacy statement #01003a");
             $scope.continueToLogin = function(){
                  console.log("welcome agreed to privacy statement #01003b");
                  utilityFunctionsService.setCookie("privacy",true,license_duration_days);
                  $state.go('welcome.login');
             };
        }
    });
});