// These are general functions used in several states.
iotgatewayhub.expandHub_repositories = function($scope,$http,$state,$uibModal,workerProcessRestService) {

    $scope.openCreateRepositoryModal = function(){
        if($scope.package_activity){
            console.log("suppressing action: current package activity");
            return;
        }

        tempURL = '/api/repository';
        workerProcessRestService.informGeneralChecking('GET', tempURL, true, false, null,
            'Manage Repositories', '', 'Getting repositories list...',
            function successCallback(data) {
                $scope.repositories = data.list;
                $scope.openCreateRepositoryModal_2();
            }, rest_check_interval_milliseconds);
    };

    $scope.openCreateRepositoryModal_2 = function(){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.manage.repositories.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http){

                // Network
                $scope.hideCancel=true;
                $scope.saveText = "Save settings";
                $scope.updated_repositories  = false;
                $scope.updating_repositories = false;

                $scope.removeRepository = function(_name){
                    $scope.repositories = $scope.repositories.filter(function(_repo){  return _repo.name !== _name; });
                };

                $scope.findRepository = function(_name){
                    var source = $scope.repositories;
                    for (var i = 0; i < source.length; i++) {
                        if (source[i].name === _name) {
                            return source[i];
                        }
                    }
                };

                $scope.updateRepos = function(){
                    if($scope.deleting_any_repository || $scope.updating_repositories || $scope.updated_repositories || $scope.adding_repository){
                        return;
                    }
                    $scope.response_message      = null;
                    $scope.error_message         = null;
                    $scope.updating_repositories = true;
                    $scope.error_message_add         = null;
                    $scope.response_message_add      = null;

                    tempURL = '/api/repository';
                    workerProcessRestService.performGeneralExecution('PUT', tempURL, true, false, null,
                        function successCallback(data) {
                            $scope.updating_repositories = false;
                            $scope.$emit("hub.unlockModal");
                            $scope.updated_repositories = true;

                            try {
                                var p_list = JSON.parse(data.p_list);
                                $scope.setPackages(p_list);
                            } catch(err) {
                                // ignore error
                                console.log(err.message);
                            }

                            $scope.reponse_message = "The repositories were successfully updated.";
                        }, function errorCallback(errorMessage) {
                            $scope.$emit("hub.unlockModal");
                            $scope.error_message = errorMessage;
                            $scope.updating_repositories = false;
                        }, rest_check_interval_milliseconds_m);

                    $scope.$emit("hub.lockModal", [$scope]);
                };

                $scope.addRepo = function(){
                    if($scope.deleting_any_repository || $scope.adding_repository || $scope.updating_repositories){
                        return;
                    }

                    $scope.adding_repository     = true;
                    $scope.error_message         = null;
                    $scope.error_message_add     = null;
                    $scope.response_message      = null;
                    $scope.response_message_add  = null;
                    var _repository_name      = $scope.newRepository.name;
                    var _repository_url       = $scope.newRepository.url;
                    var _repository_username  = $scope.newRepository.username;
                    var _repository_password  = $scope.newRepository.password;

                    var _existing = $scope.findRepository(_repository_name);

                    if(!_repository_name){
                        $scope.error_message_add         =  "The repository name must be specified.";
                        $scope.adding_repository     = false;
                        return;
                    }
                    if(!_repository_url){
                        $scope.error_message_add         =  "The repository url must be specified.";
                        $scope.adding_repository     = false;
                        return;
                    }
                    if(_existing){
                        $scope.error_message_add         =  "The repository "+_repository_name+" already exists.";
                        $scope.adding_repository     = false;
                        return;
                    }

                    if(!_repository_username){
                        _repository_username = "None"; //see API document. This is exactly what is specified.
                    }
                    if(!_repository_password){
                        _repository_password = "None"; //see API document. This is exactly what is specified
                    }

                    //form validation---for now just checking name,url have values, and that name is unique
                    //according to Nick at Intel the auth credentials (userName and password) are to be optional here

                    if(_repository_name && _repository_url) {
                        //console.log("adding repository");
                        $scope.response_message_add = "Adding repository "+_repository_name+". Package list will be updated. This may take a few minutes...";

                        tempURL = '/api/repository'+
                                  "?name="+encodeURIComponent(_repository_name)+
                                  "&url="+encodeURIComponent(_repository_url)+
                                  "&username="+encodeURIComponent(_repository_username)+
                                  "&password="+encodeURIComponent(_repository_password);
                        workerProcessRestService.performGeneralExecution('POST', tempURL, false, false, null,
                            function successCallback(data) {
                                $scope.adding_repository  = false;
                                $scope.response_message_add = '';
                                $scope.response_message_add = "The repository "+_repository_name+" was successfully added. Package list was updated.";
                                //clear the form values and add the repo to the list
                                $scope.newRepository.name = '';
                                $scope.newRepository.url  = '';
                                try {
                                    $scope.repositories.push(_repository_name);
                                } catch (err) {
                                    console.log(err.message);
                                }

                                try {
                                    var p_list = JSON.parse(data.p_list);
                                    $scope.setPackages(p_list);
                                } catch(err) {
                                    // ignore error
                                    console.log(err.message);
                                }

                                $scope.$emit("hub.unlockModal", [$scope]);
                                console.log("added repository "+_repository_name);
                            }, function errorCallback(errorMessage) {
                                $scope.adding_repository  = false;
                                $scope.response_message_add = '';
                                $scope.error_message_add = errorMessage;

                                $scope.$emit("hub.unlockModal", [$scope]);
                            }, rest_check_interval_milliseconds_m);

                        $scope.$emit("hub.lockModal", [$scope]);
                    }

                };

                $scope.closeModal = function(){
                    if($scope.adding_repository || $scope.updating_repositories){
                        return;
                    }
                    modalInstance.dismiss('cancel');
                };

                $scope.deleteRepo = function(_repository){
                    console.log("hub deleting repository "+_repository+" #4084a");
                    if($scope.deleting_any_repository || $scope.adding_repository || $scope.updating_repositories){
                        return;
                    }
                    $scope.error_message                          = '';
                    $scope.error_message_add         = null;
                    $scope.response_message_add      = null;
                    $scope.response_message          = null;
                    $scope.deleting_repository[_repository]  = true;
                    $scope.deleting_any_repository           = true;

                    tempURL = '/api/repository'+"?name="+encodeURIComponent(_repository);
                    workerProcessRestService.performGeneralExecution('DELETE', tempURL, false, false, null,
                        function successCallback(data){
                            $scope.deleting_repository[_repository]  = false;
                            $scope.deleting_any_repository           = false;

                            try {
                                var _index = $scope.repositories.indexOf(_repository);
                                if (_index >= 0) {
                                    $scope.repositories.splice(_index, 1);
                                    // console.log($scope.repositories);
                                }

                            } catch(err) {
                                console.log(err.message);
                            }

                            try {
                                var p_list = JSON.parse(data.p_list);
                                $scope.setPackages(p_list);
                            } catch(err) {
                                // ignore error
                                console.log(err.message);
                            }

                            $scope.response_message   = "The repository "+_repository+" was successfully deleted.";
                        }, function errorCallback(errorMessage) {
                            $scope.deleting_repository[_repository]  = false;
                            $scope.deleting_any_repository           = false;
                            $scope.error_message = errorMessage;
                        }, rest_check_interval_milliseconds_m);
                };
            }
        });
    };
};
