iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    /* NOTE: this state uses the networksettings DIRECTIVE */

    $stateProvider.state('hub.networksettings',{
        url:         '/hub/networksettings',
        templateUrl: '/partials/hub.networksettings.main.ng.html?version=' + gui_version,
        scope:false,
        controller: function($scope,$state,$http,$rootScope){
            $scope.cancelText="Skip";
            $scope.hideTestSettings=true;
            $scope.maintainContextAfterSave = false;

            $scope.skipSettings = function(){
                $state.go('hub.dashboard.sensors',{});

            };

            $scope.doneSuccessCallback = function(){
                $state.go('hub.networkconnected',{});
            };
        }
    });
});
