// Used in HTML as ps-mouse-over
iotgatewayhub.directive('psMouseOver', function () {
    return {       
        link: function(scope, element) {
            element.bind("mouseover", function(e){
            	console.log( 'mousever');
                e.stopPropagation();
                e.preventDefault();    
                element.perfectScrollbar('update');
            });   
        }
    };
});