// These are general functions used in several states.
iotgatewayhub.expandHub_packages = function($scope,$state,$http,$rootScope,$uibModal,$timeout,workerProcessRestService) {

    var PACKAGE_ACTIVITY_TYPE = {
        INSTALL: "INSTALL",
        UNINSTALL: "UNINSTALL",
        UPDATE: "UPDATE",
        UPDATE_ALL: "UPDATE_ALL",
        SELF_UPDATE: "SELF_UPDATE"
    };

    var PACKAGE_STATE = {
        INSTALLED: "INSTALLED",
        UNINSTALLED: "UNINSTALLED",
        UPDATED:"UPDATED",
        UPDATE_FAILED:"UPDATE_FAILED"
    };

    // The Global State //
    $scope.$on('packageActivityStart', function(opt, args){
        var $s = opt.currentScope;
        if(args){
            var _name = args[0] ?  args[0] : '';
            var _type= args[1] ?  args[1] : '';
        }

        $s.package_activity = true;

        switch(_type){
            case PACKAGE_ACTIVITY_TYPE.INSTALL:
                $s.installing_package[_name] = true;
                $s.installing_any_package     = true;
                break;

            case PACKAGE_ACTIVITY_TYPE.UNINSTALL:
                $s.uninstalling_package[_name] = true;
                break;

            case PACKAGE_ACTIVITY_TYPE.UPDATE:
                $s.updating_package[_name] = true;
                break;

            case PACKAGE_ACTIVITY_TYPE.SELF_UPDATE:
                $s.selfUpgrading = true;
                $s.updating_package[_name] = true;
                break;

            case PACKAGE_ACTIVITY_TYPE.UPDATE_ALL:
                $s.installing_all_package_updates = true;
                break;

        }
    });

    $scope.$on('packageActivityStop', function(opt, args){
        var $s = opt.currentScope;

        if(args){
            var _name = args[0] ?  args[0] : '';
            var _type= args[1] ?  args[1] : '';
        }
        // manages package activity states, regardless of success/failure

        $s.package_activity = false;

        switch(_type){
            case PACKAGE_ACTIVITY_TYPE.INSTALL:
                $s.installing_package[_name] = false;
                $s.installing_any_package     = false;
                break;

            case PACKAGE_ACTIVITY_TYPE.UNINSTALL:
                $s.uninstalling_package[_name] = false;
                break;

            case PACKAGE_ACTIVITY_TYPE.UPDATE:
                $s.updating_package[_name] = false;
                break;

            case PACKAGE_ACTIVITY_TYPE.SELF_UPDATE:
                $s.selfUpgrading = false;
                $s.updating_package[_name] = false;
                break;

            case PACKAGE_ACTIVITY_TYPE.UPDATE_ALL:
                $s.installing_all_package_updates = false;
        }
    });

    ///////////////////////////////////////
    // Package functions - basic ops
    //////////////////////////////////////

    function basicInstallPackage(_name,_trust, successCallback, errorCallback) {
        var _untrusted_query = "&rpm=untrusted";
        tempURL = '/api/packages?package='+encodeURIComponent(_name)+(_trust?_untrusted_query:'');
        workerProcessRestService.performGeneralExecution('POST', tempURL, false, false, null,
            successCallback, errorCallback, rest_check_interval_milliseconds_m);
    }

    function basicUninstallPackage(_name, successCallback, errorCallback) {
        tempURL = '/api/packages?package='+encodeURIComponent(_name);
        workerProcessRestService.performGeneralExecution('DELETE', tempURL, false, false, null,
            successCallback, errorCallback, rest_check_interval_milliseconds_m);
    }

    function basicUpdatePackage(_name, _trust, successCallback, errorCallback) {
        var _untrusted_query = "&rpm=untrusted";
        tempURL = '/api/packages?package='+encodeURIComponent(_name)+(_trust?_untrusted_query:'');
        workerProcessRestService.performGeneralExecution('PUT', tempURL, false, false, null,
            successCallback, errorCallback, rest_check_interval_milliseconds_m);
    }

    function basicUpdateAllPackages(_trust, successCallback, errorCallback) {
        var _untrusted_query = "&rpm=untrusted";
        tempURL = '/api/packages?package=all'+(_trust?_untrusted_query:'');
        workerProcessRestService.performGeneralExecution('PUT', tempURL, false, false, null,
            successCallback, errorCallback, rest_check_interval_milliseconds_l);
    }

    function basicSelfUpgrade(successCallback, errorCallback) {
        tempURL = '/api/selfupgrade';
        workerProcessRestService.performGeneralExecutionOnlySubmit('PUT', tempURL, true, false, null,
            successCallback, errorCallback);
    }

    ///////////////////////////////////////
    // Package functions
    //////////////////////////////////////

    $scope.checkForPackageActivity = function(){
        return $scope.package_activity || $scope.installing_any_package; // true value means block IO
    };

    function changePackageState(_package, state, blockSetArr, message ){

        // Modifies the models

        switch(state){
            case PACKAGE_STATE.INSTALLED:
                _package.installed = true;
                $scope.installed_package[_package.name] = true;
                break;

            case PACKAGE_STATE.UNINSTALLED:
                _package.installed = false;
                $scope.installed_package[_package.name]  = false;
                _.remove($scope.installed, { 'name': _package.name });
                if(!blockSetArr) $scope.setArrays(true);
                break;

            case PACKAGE_STATE.UPDATED:
                $scope.updating_package[_package.name] = false;
                _package.version        =_package.upgrade_version;
                _package.upgrade_version= null;
                $scope.updated_package[_package.name] = true;
                if(!blockSetArr) $scope.setArrays(false);
                break;

            case PACKAGE_STATE.UPDATE_FAILED:
                if (typeof message === "undefined") {
                    update_message = '';
                } else {
                    update_message = message;
                }
                $scope.updating_package[_package.name] = false;
                $scope.update_package_failed[_package.name] = true;
                $scope.update_package_fail_message[_package.name] = update_message;
                break;
        }
    }

    $scope.showPackageInfo = function(_package){
        tempURL = '/api/packageinfo?name='+_package.name;
        workerProcessRestService.informGeneralChecking('GET', tempURL, false, false, null,
            _package.name, '', 'Getting Package Information...',
             function successCallback(data) {
                var _details = data;
                for (var attrname in _details) { _package[attrname] = _details[attrname]; }
                $scope.openPackageInfoModal(_package);
             }, rest_check_interval_milliseconds_m);
    };

    $scope.installPackage = function(_package,_success,_error,_trust){
        var _name = _package.name;
        $scope.message = '';

        // Check for blockers
        if(_package.installed){
            console.log("hub control not installing package "+_name+ " because it is already installed #9033z");
            return;
        }

        if($scope.checkForPackageActivity()){
            console.log("hub control not installing package "+_name+ " because package activity in progress #9033q");
            return;
        }

        // Start install
        $rootScope.$broadcast('packageActivityStart', [_name, PACKAGE_ACTIVITY_TYPE.INSTALL]);
        basicInstallPackage(_name, _trust,
            function successCallback(data) {
                $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.INSTALL]);
                if (data.status==="success") {
                    changePackageState(_package, PACKAGE_STATE.INSTALLED);

                    try {
                        var p_list = JSON.parse(data.p_list);
                        $scope.setPackages(p_list);
                    } catch(e) {
                        // ignore error
                    }

                    $timeout(function(){
                        $scope.setArrays(true);
                        if(_.isFunction(_success)){ _success(); }
                    },1500);
                } else if (data.status==='untrusted' && !_trust){
                    //$scope.openInstallUntrustedPackageModal(_package, data.error)
                    _package.confirmUntrusted = true;
                } else {
                    $scope.openInstallPackageErrorModal('Package Install Error', _package, data.message);
                    if(_.isFunction(_error)){_error();}
                }
            }, function errorCallback(errorMessage) {
                $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.INSTALL]);
                $scope.openInstallPackageErrorModal('Package Install Error', _package, errorMessage);
                if(_.isFunction(_error)){_error();}
            });
    };

    $scope.uninstallPackage = function(_package){
        var _name = _package.name;
        $scope.message = '';
        if($scope.uninstalling_package[_package.name]){
            return;
        }

        $rootScope.$broadcast('packageActivityStart', [_name, PACKAGE_ACTIVITY_TYPE.UNINSTALL]);

        basicUninstallPackage(_package.name,
            function successCallback(data) {
                $scope.package_activity = false;
                $scope.uninstalling_package[_package.name] = false;
                changePackageState(_package, PACKAGE_STATE.UNINSTALLED);
                $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.UNINSTALL]);

                try {
                    var p_list = JSON.parse(data.p_list);
                    $scope.setPackages(p_list);
                } catch(e) {
                    // ignore error
                }

            }, function errorCallback(errorMessage) {
                $scope.package_activity = false;
                $scope.uninstalling_package[_package.name] = false;
                $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.UNINSTALL]);
                $scope.message = errorMessage;
                $scope.openInstallPackageErrorModal('Package Uninstall Error', _package, errorMessage);
            });
    };

    $scope.launchPackage = function(_name,_url){
        console.log("launching package "+_name+" at url:"+_url+" #003v");
        //if($scope.checkForPackageActivity()){
        //    return;
        //}

        var win = window.open(window.location.protocol+"//"+_url, '_blank');
        win.focus();
    };

    $scope.updatePackage = function(_package,_success,_error,_trust){

        var _name    = _package.name;
        var _version = _package.upgrade_version;
        if(_version===''){
            console.log("hub control NOT updating package "+_name+" because upgrade_version is '' #30255z");
            return;
        }

        if(_package.name === 'iot-developer-hub'){
            $scope.openUpdateDevHubPackageModal(_package);
            return;
        }

        $rootScope.$broadcast('packageActivityStart', [_name, PACKAGE_ACTIVITY_TYPE.UPDATE]);

        basicUpdatePackage(_name, _trust,
            function successCallback(data) {
                $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.UPDATE]);
                if (data.status === "success") {

                    if ('p_info' in data) {
                        var _details = data.p_info;
                        for (var attrname in _details) { _package[attrname] = _details[attrname]; }
                    }

                    try {
                        var p_list = JSON.parse(data.p_list);
                        $scope.setPackages(p_list);
                    } catch(e) {
                        // ignore error
                    }

                    changePackageState(_package,PACKAGE_STATE.UPDATED);
                    if (_.isFunction(_success)) {_success();}
                } else if (data.status === 'untrusted' && !_trust) {
                    $scope.openUpdateUntrustedPackageModal(_package,data.error,_success);
                    return;
                } else {
                    updateFailed(_package, data.message);
                }
            }, function errorCallback(errorMessage) {
                updateFailed(_package, errorMessage);
            });

        function updateFailed(_package, errorMessage){
            $rootScope.$broadcast('packageActivityStop', [_name, PACKAGE_ACTIVITY_TYPE.UPDATE]);

            if(_.isFunction(_error)){_error(errorMessage);}

            changePackageState(_package, PACKAGE_STATE.UPDATE_FAILED, false, errorMessage);

            // ui-related hacks
            $scope.update_package_failed[_package.name] = true;
            $scope.update_package_fail_message[_package.name] = errorMessage;
            $timeout(function(){
                $scope.update_package_failed[_package.name] = false;
            }, linger_info_timeout_milliseconds);
        }
    };

    $scope.updateDevHub = function(_package, _success, _error){

        $rootScope.$broadcast('packageActivityStart', [_package.name, PACKAGE_ACTIVITY_TYPE.SELF_UPDATE]);

        basicSelfUpgrade(
            function successCallback(data) {
                $rootScope.$broadcast('packageActivityStop', [_package.name, PACKAGE_ACTIVITY_TYPE.SELF_UPDATE]);
                changePackageState(_package, PACKAGE_STATE.UPDATED);
                if(_.isFunction(_success)){ _success();}
            }, function errorCallback(errorMessage) {
                $rootScope.$broadcast('packageActivityStop', [_package.name, PACKAGE_ACTIVITY_TYPE.SELF_UPDATE]);

                if(_.isFunction(_error)){ _error(errorMessage);}

                changePackageState(_package, PACKAGE_STATE.UPDATE_FAILED, false, errorMessage);

                // ui-related hacks
                $scope.update_package_failed[_package.name] = true;
                $scope.update_package_fail_message[_package.name] = errorMessage;
                $timeout(function(){
                    $scope.update_package_failed[_package.name] = false;
                }, linger_info_timeout_milliseconds);
            });
    };

    $scope.updatePackagesIndividually = function(_trust){

        console.log("updating packages individually");

        var selfUpgradeAvailable = null;

        var packagesToUpdate = [];

        $rootScope.$broadcast('packageActivityStart', [null,PACKAGE_ACTIVITY_TYPE.UPDATE_ALL]);

        // Build list to update
        _.forEach($scope.packages, function(_package){

            if(!_package.upgrade_version) return;

            if(_package.name === "iot-developer-hub"){
                selfUpgradeAvailable = _package;
            } else {
                packagesToUpdate.push(_package);
                //$scope.update_package_queue[_package.name] = true;
            }

        });

        if(packagesToUpdate.length < 1){
            if(selfUpgradeAvailable){
                $scope.openUpdateDevHubPackageModal(selfUpgradeAvailable);
            }
            return;
        }

        var i=0;

        function updateNextPackage(){
            if(packagesToUpdate[i]){
                console.log("updating packages individually: ("+i+")"+ packagesToUpdate[i].name);
                $scope.updatePackage(packagesToUpdate[i],
                    function() {
                        i+=1;
                        updateNextPackage();
                    }, function(errorMessage) {
                        $rootScope.$broadcast('packageActivityStop', [null,PACKAGE_ACTIVITY_TYPE.UPDATE_ALL]);
                        $scope.openInstallPackageErrorModal('Package Update Error', packagesToUpdate[i], errorMessage + ' Please refresh the web page to get updated packages information!');
                    });
                return;
            }
            if(selfUpgradeAvailable){
                console.log("updating packages individually: 'iot-developer-hub', launching confirmation");
                $scope.openUpdateDevHubPackageModal(selfUpgradeAvailable);

            }
        }

        updateNextPackage();
    };

    $scope.updatePackages = function(_trust){

        var selfUpgradeAvailable = function(){
            var devHubPackage = _.find($scope.packages, function(_p) { return _p.name === "iot-developer-hub"; });

            if(devHubPackage && devHubPackage.upgrade_version){
             return devHubPackage;
            }else{
                return null;
            }
        }();

        if(selfUpgradeAvailable){
            $scope.updatePackagesIndividually(_trust);
            return;
        }

        if($scope.installing_all_package_updates || $scope.package_activity){
            return;
        }

        $rootScope.$broadcast('packageActivityStart', [null, PACKAGE_ACTIVITY_TYPE.UPDATE_ALL]);

        _.forEach($scope.packages, function(_package){

            if(!_package.upgrade_version) return;

            $scope.updating_package[_package.name] = true;
        });

        basicUpdateAllPackages(_trust,
            function successCallback(data) {
                $rootScope.$broadcast('packageActivityStop', [null,PACKAGE_ACTIVITY_TYPE.UPDATE_ALL]);
                if (data.status === 'success') {
                    for(var _p=0,_pl=$scope.packages.length;_p<_pl;_p++){
                        changePackageState($scope.packages[_p], PACKAGE_STATE.UPDATED, true);
                    }

                    try {
                        var p_list = JSON.parse(data.p_list);
                        $scope.setPackages(p_list);
                    } catch(e) {
                        // ignore error
                    }

                    $scope.setArrays(false);
                } else if (data.status === 'untrusted' && !_trust){
                    $scope.openUpdateUntrustedPackagesModal(data.error);
                    return;
                } else {
                    errorCallback(data.message);
                }
            }, errorCallback);

        function errorCallback(errorMessage) {
            $rootScope.$broadcast('packageActivityStop', [null,PACKAGE_ACTIVITY_TYPE.UPDATE_ALL]);
            for(var _p=0,_pl=$scope.packages.length;_p<_pl;_p++){
                var _packg = $scope.packages[_p];
                changePackageState(_packg, PACKAGE_STATE.UPDATE_FAILED, true, errorMessage);
            }

            $timeout(function(){
                for(var _p=0,_pl=$scope.packages.length;_p<_pl;_p++){
                    var _packg = $scope.packages[_p];
                    $scope.update_package_failed[_packg.name] = false;
                }
            }, linger_info_timeout_milliseconds);

            var tempPackage = {};
            tempPackage.name = 'Update Packages';
            $scope.openInstallPackageErrorModal('Packages Update Error', tempPackage, errorMessage);
        }
    };

    $scope.setPackages = function(package_list, passedSuccessCallback, passedErrorCallback){
        console.log("hub controller: getting packages #005a");

        if ((typeof package_list === 'undefined') || (typeof package_list === 'string')) {
            $http({
                method : 'GET',
                url    : '/api/packages?nocache=' + new Date().getTime()
            }).then(
                function successCallback(response) {
                    $scope.packages = response.data;
                    console.log("hub controller: found packages by API call 005b");
                    $scope.package_refresh = Number($scope.package_refresh) + 1;
                    console.log("hub controller: setting package refresh flag 005c");

                    if(_.isFunction(passedSuccessCallback)){ passedSuccessCallback(response.data); }
                },function errorCallback(response) {
                    console.log("error getting packages 005E");

                    if(_.isFunction(passedErrorCallback)){ passedErrorCallback(response); }
                });
        } else {
            $scope.packages = package_list;
            $scope.package_refresh = Number($scope.package_refresh) + 1;
        }
    };

    ///////////////////////////////////////
    // GUI Modals
    //////////////////////////////////////

    $scope.openPackageInfoModal = function(_package){

        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.package.info.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.service_error = false;
                $scope.displayed   = _package;

                //$scope.statuses    = $scope.service_statuses;
                $scope.running     = $scope.package_statuses[_package.name].running;

                //var _state         = $scope.package_states[_package.name];
                //console.log("hub control opening package info modal for "+_package.name+" with State "+_state);

                $scope.enabled     =  $scope.package_statuses[_package.name].autorun;
                $scope.checkbox    = {enabled: $scope.enabled};
                console.log("hub control opening package info modal for "+_package.name+" with State "+$scope.enabled+" "+$scope.running);
                $scope.hasupgrade  =  _package.upgrade_version;

                $scope.closePackageInfo = function(){
                    console.log("close packageinfo");
                    modalInstance.dismiss('cancel');
                    $scope.installed_package = {};
                };

                $scope.toggleServiceState = function(_enable){
                    if($scope.enabling_package[_package.name]){
                        return;
                    }
                    $scope.enabling_package[_package.name] = true;

                    var _len         = _package.service.length;
                    var _action      = _enable ? "enable"    : "disable";
                    console.log("hub service control toggling service state for "+_package.name+" to "+_action+" #5502a");

                    var _services = null;
                    if(_package.service){
                        _services = _package.service.join(",");
                        if(_services){
                            tempURL = '/api/sc?services=' + _services + "&action=" + _action;
                            workerProcessRestService.performGeneralExecution('POST', tempURL, false, false, null,
                                function successCallback(data) {
                                    $scope.enabling_package[_package.name] = false;
                                    if (data.status === 'success'){
                                        if(_enable){
                                            console.log("hub service control successfully changed service state for "+_package.name+" to "+_action+" #5502c1");
                                        }else{
                                            console.log("hub service control successfully changed service state for "+_package.name+" to "+_action+" #5502c2");
                                        }
                                        //$scope.package_statuses[_package.name].State = _enable? 'enabled' : 'disabled';
                                        $scope.setArrays(true);
                                    } else {
                                        $scope.enabling_package[_package.name] = false;
                                        $scope.service_error = true;
                                        $scope.service_error_message = 'This action failed. Please try again! ' + data.message;
                                        if (_enable) {
                                            // enable failed.... uncheck checkbox
                                            $scope.checkbox    = {enabled: false};
                                        } else {
                                            // disable failed.... check checkbox
                                            $scope.checkbox    = {enabled: true};
                                        }
                                        console.log("hub service control failed to change service state for "+_package.name+" to "+_action+" #5502F");

                                        setTimeout(function(){
                                            $scope.service_error = false;
                                        }, linger_info_timeout_milliseconds);
                                    }
                                }, function errorCallback(errorMessage) {
                                    $scope.enabling_package[_package.name] = false;
                                    $scope.service_error = true;
                                    $scope.service_error_message = errorMessage;
                                    if (_enable) {
                                        // enable failed.... uncheck checkbox
                                        $scope.checkbox    = {enabled: false};
                                    } else {
                                        // disable failed.... check checkbox
                                        $scope.checkbox    = {enabled: true};
                                    }
                                    console.log("errorCallback with " + errorMessage);

                                    setTimeout(function(){
                                        $scope.service_error = false;
                                    }, linger_info_timeout_milliseconds);
                                }, rest_check_interval_milliseconds_s);
                        }
                    }
                };

                $scope.doUpdate = function(_displayed){
                    $scope.updatePackage(_displayed, function(){
                        $scope.setArrays(true);
                        //refactor out
                    }, function errorCallback(errorMessage){
                        $scope.openInstallPackageErrorModal('Package Update Error', _displayed, errorMessage);
                        $scope.setArrays(true);
                        modalInstance.dismiss('cancel');
                    });
                };
            }
        });
    };

    $scope.openUpdateUntrustedPackagesModal = function(_message){
        var modalInstance2 = $uibModal.open({
            templateUrl: 'partials/hub.update.untrusted.packages.ng.html?version=' + gui_version,
            animation:    true,
            scope:        $scope,
            size:        'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.message      = _message;
                $scope.closeModal = function(){
                    modalInstance2.dismiss('cancel');
                };
                $scope.trustToUpdate = function(){
                    modalInstance2.dismiss('cancel');
                    $scope.updatePackages(true);
                };
            }
        });
    };

    $scope.confirmUpdatePackages = function(){
        console.log("hub confirming update packages #9190a");
        if($scope.package_activity){
            console.log("suppressing action: current package activity");
            return;
        }
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.confirm.update.packages.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.doUpdatePackages = function(){
                    modalInstance.dismiss('cancel');
                    $scope.updatePackages();
                };
            }
        });
    };

    $scope.openUpdateDevHubPackageModal = function(_package){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.confirm.update.devhub.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                //$scope.selected_device   = null;

                $rootScope.$broadcast('packageActivityStart');
                $scope.selfUpgrading = false;

                $scope.closeModal = function(){
                    $rootScope.$broadcast('packageActivityStop');
                    console.log("close confirm restart modal");
                    modalInstance.dismiss('cancel');
                };

                $scope.doSelfUpdate =  function(){
                    $scope.selfUpgrading = true;
                    $scope.updateDevHub(_package,
                        function successCallback(){
                            // stop checking so that we won't throw no network error.
                            // later on, when we load state.hub, we will reschedule it.
                            $scope.stopCheckingNetwork();

                            $timeout(function(){
                                modalInstance.dismiss('cancel');
                                $state.go("welcome.login");

                            },5000);
                        },
                        function errorCallback(){
                            $timeout(function(){
                                $scope.update_package_failed[_package.name] = false;
                            }, linger_info_timeout_milliseconds);

                            $scope.openInstallPackageErrorModal();

                            //Assume success
                            $timeout(function(){
                                modalInstance.dismiss('cancel');
                                $state.go("welcome.login");

                            }, 7000);

                        }
                    );
                };
            }
        });
    };

    $scope.startTrustedInstall = function (_package) {
        _package.confirmUntrusted=false;
        if($scope.checkForPackageActivity()){
            return;
        }
        $scope.installPackage(_package, null, null, true);
    };

    $scope.openUpdateUntrustedPackageModal = function(_package,_message, _success){
        var modalInstance2 = $uibModal.open({
            templateUrl: 'partials/hub.update.untrusted.package.ng.html?version=' + gui_version,
            animation:    true,
            scope:        $scope,
            size:        'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.installed_package[_package.name] = false;
                $scope.untrusted    = _package;
                $scope.message      = _message;
                $scope.closeModal = function(){
                    modalInstance2.dismiss('cancel');
                };
                $scope.trustToUpdate = function(){
                    modalInstance2.dismiss('cancel');
                    $scope.updatePackage(_package,  _success, null, true);
                };
            }
        });
    };

    $scope.openInstallUntrustedPackageModal = function(_package,_message){
        var modalInstance2 = $uibModal.open({
            templateUrl: 'partials/hub.install.untrusted.package.ng.html?version=' + gui_version,
            animation:    true,
            scope:        $scope,
            size:        'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.installed_package[_package.name] = false;
                $scope.untrusted    = _package;
                $scope.message      = _message;
                $scope.closeModal = function(){
                    modalInstance2.dismiss('cancel');
                };
                $scope.trustToInstall = function(){
                    modalInstance2.dismiss('cancel');

                    $scope.installPackage(
                        _package,
                        function(){
                            //modalInstance2.dismiss('cancel');
                        },
                        function(){
                            //modalInstance2.dismiss('cancel');
                        },true);
                };

            }
        });
    };

    $scope.openInstallPackageErrorModal = function(_title, _package, _message){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.install.package.error.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.title = _title;
                $scope.package = _package;
                $scope.message = _message;
                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openServiceActivityPackageErrorModal = function(_title, _message){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.service.package.error.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope,$timeout){
                $scope.title = _title;
                $scope.message = _message;
                $scope.closeModal = function(){
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openAddPackagesModal = function(){

        if($scope.package_activity){
            console.log("suppressing action: current package activity");
            return;
        }

        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.add.packages.ng.html?version=' + gui_version,
            animation:    true,
            scope: $scope,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            controller: function($scope,$state,$http,$rootScope){

                $scope.query = null;
                $scope.sortType = 'name';
                $scope.installed_package    = {};

                $scope.closeAddPackages = function(){
                    if($scope.installing_any_package) return;
                    $scope.installed_package    = {};
                    modalInstance.dismiss('cancel');

                    console.log("closing add packages modal.");
                };

            }
        });

    };
};
