// Used in HTML as hub-dashboard-linegraph
iotgatewayhub.directive('hubDashboardLinegraph', function () {
    return {
        restrict: 'E',
        templateUrl: 'partials/hub.dashboard.linegraph.ng.html?version=' + gui_version,
        replace: true,
        scope: {
              latestData: '=',
              scaleSteps: '=',
              scaleStepWidth: '=',
              scaleStartValue: '=',
              pointsToShow: '=',
              units: '=',
              connected: '='
        },
        link: function ($scope, $elem, attrs) {
            var datasets = {

            };

            $scope.connected = true;

            var setOptions = function() {

                var overrideScale = ($scope.scaleSteps !== null &&
                            $scope.scaleSteps !== undefined &&
                            $scope.scaleStepWidth !== null &&
                            $scope.scaleStepWidth !== undefined &&
                            $scope.scaleStartValue !== null &&
                            $scope.scaleStartValue !== undefined);

                $scope.options = {
                    animation: false,
                    showScale: true,
                    showTooltips: false,
                    maintainAspectRatio: false,

                    scaleOverride: overrideScale,
                    scaleSteps: parseInt($scope.scaleSteps),
                    scaleStepWidth: parseFloat($scope.scaleStepWidth),
                    scaleStartValue: parseFloat($scope.scaleStartValue),
                    scaleFontColor: "#ffffff",

                    // String - Message for empty data
                    emptyDataMessage: " ",

                    ///Boolean - Whether grid lines are shown across the chart
                    scaleShowGridLines : true,

                    //String - Colour of the grid lines
                    scaleGridLineColor : "rgba(0,0,0,.05)",

                    //Number - Width of the grid lines
                    scaleGridLineWidth : 1,

                    //Boolean - Whether to show horizontal lines (except X axis)
                    scaleShowHorizontalLines: true,

                    //Boolean - Whether to show vertical lines (except Y axis)
                    scaleShowVerticalLines: false,

                    // String - scale type: "number" or "date"
                    scaleType: "date",

                    // Boolean - Whether to use UTC dates instead local
                    useUtc: false,

                    // String - short time format (used for scale labels)
                    scaleTimeFormat: " ",

                    //Boolean - Whether the line is curved between points
                    bezierCurve : true,

                    //Number - Tension of the bezier curve between points
                    bezierCurveTension : 0.4,

                    //Boolean - Whether to show a dot for each point
                    pointDot : false,

                    //Number - Radius of each point dot in pixels
                    pointDotRadius : 2,

                    //Number - Pixel width of point dot stroke
                    pointDotStrokeWidth : 1,

                    //Number - amount extra to add to the radius to cater for hit detection outside the drawn point
                    pointHitDetectionRadius : 1,

                    //Boolean - Whether to show a stroke for datasets
                    datasetStroke : true,

                    //Number - Pixel width of dataset stroke
                    datasetStrokeWidth : 3,

                    //Boolean - Whether to fill the dataset with a colour
                    datasetFill : false,

                    legendTemplate : "<div></div>"

                };
            };

            var trimHistory = function () {
                while ($scope.scatterChart.datasets[0].points.length > $scope.pointsToShow) {
                    $scope.scatterChart.datasets[0].removePoint(0);
                    $scope.dataHistory[0].data.shift();
                }
                // if ($scope.dataHistory[0].data) {
                //     while ($scope.dataHistory[0].data.length > $scope.pointsToShow) {
                //         $scope.dataHistory[0].data.shift();
                //     }
                // }
            };

            $scope.dataHistory = [
                {
                    label: '',
                    strokeColor: '#ffffff',
                    data: [
                        {x: $scope.latestData.x, y: $scope.latestData.y}
                    ]
                }
            ]; // must be 1 level deep to provide data in a series for chart.js

            setOptions();
            var chartCanvasFind = $elem.find("canvas");
            console.log("hub.dashboard.linegraph.js ", chartCanvasFind, chartCanvasFind.length);
            var canvasContext = chartCanvasFind[0].getContext("2d");
            $scope.scatterChart = new Chart(canvasContext).Scatter($scope.dataHistory, $scope.options);

            if ($scope.latestData !== null && $scope.latestData !== undefined) {
                $scope.scatterChart.datasets[0].addPoint($scope.latestData.x, $scope.latestData.y);
                $scope.dataHistory[0].data.push({x:$scope.latestData.x, y:$scope.latestData.y});
            }

            $scope.series = [""];

            $scope.$watchGroup(['scaleStepWidth', 'scaleStartValue', 'scaleSteps'], function() {

                setOptions();
                $scope.scatterChart.destroy();
                $scope.scatterChart = new Chart(canvasContext).Scatter($scope.dataHistory, $scope.options);
            });

            $scope.$watch('latestData', function() {

                if ($scope.latestData !== null && $scope.latestData !== undefined) {
                   $scope.scatterChart.datasets[0].addPoint($scope.latestData.x,$scope.latestData.y);
                   $scope.dataHistory[0].data.push({x:$scope.latestData.x, y:$scope.latestData.y});
                }
                trimHistory();

                $scope.scatterChart.update();
            });

            $scope.$watch('pointsToShow', function() {
               trimHistory();
               $scope.scatterChart.update();
            });

            $scope.$watch('connected', function() {
                if ($scope.connected) {
                    $scope.dataHistory[0].strokeColor = "#ffffff";
                } else {
                    $scope.dataHistory[0].strokeColor = "#7f868c";
                }
                $scope.scatterChart.destroy();
                $scope.scatterChart = new Chart(canvasContext).Scatter($scope.dataHistory, $scope.options);
            });

            setOptions();
        }
    };
});