iotgatewayhub.service('httpRequestsService', function($q){

    // We use service here because we need to keep track of this "toBeAbortedPromises" array.
    // For Service, an object will be instantiated.
    var toBeAbortedPromises = [];

    function customTimeout() {
        var newPromise = $q.defer();
        toBeAbortedPromises.push(newPromise);
        return newPromise.promise;
    }

    function abortAllRequests() {
        angular.forEach(toBeAbortedPromises, function (eachPromise){
            eachPromise.promise.isAbortedByUs = true;
            eachPromise.resolve();
        });
        // clear the array, and still keep the array object
        toBeAbortedPromises.length = 0;
    }

    function removeRequest(response) {
        // Once the web request has a response, we should remove it from the tracking array.
        try {
            // skip is response.config.timeout is not a promise
            if (typeof response.config.timeout === 'number') {
                return;
            } else if (response.config.timeout === undefined) {
                return;
            }

            // try to find a match in the recorded array
            var target_index = -1;
            var count = 0;
            angular.forEach(toBeAbortedPromises, function (eachPromise){
                if (eachPromise.promise === response.config.timeout) {
                    target_index = count;
                }
                count = count + 1;
            });
            if (target_index > -1) {
                // found, so remove it
                toBeAbortedPromises.splice(target_index, 1);
            }
        } catch (err) {
            // ignore error
        }
    }

    return {
        customTimeout: customTimeout,
        abortAllRequests: abortAllRequests,
        removeRequest: removeRequest
    };
});

iotgatewayhub.factory('httpRequestsInterceptor', ['$rootScope', '$q', 'httpRequestsService', function($rootScope, $q, httpRequestsService){
    return {
        'request': function (config) {
            //console.log('############### req s ####################');
            config = config || {};
            if (typeof config.url === 'string') {
                if (config.url.indexOf('.ng.html') > -1) {
                    // Note:  For Chrome, if there is no connection, it will immediately abort the request and returns status = -1.
                    //      However, For Firefox, if there is no connection,it queues the request as pending request. For user, if the user
                    //      wants to open a GUI, the GUI may appear in-responsive to the user.
                    //      Therefore, we set some timeout for html request.
                    //console.log('dealing with static content');
                    config.timeout = html_content_timeout_milliseconds;
                }
            }
            if (config.timeout === undefined) {
              // If no specific timeout is assigned, we use our custom timeout.
              //console.log('custom promise');
              config.timeout = httpRequestsService.customTimeout();
            }
            return config;
        },
        //'requestError': function (rejection) {
        //    console.log('############### resp f ####################');
        //    console.log(rejection);
        //    return $q.reject(rejection);
        //},
        'response': function (response) {
            //console.log('############### resp s ####################');
            setResponseErrorTime(false);
            httpRequestsService.removeRequest(response);
            return response;
        },
        'responseError': function (rejection) {
            //console.log('############### resp f ####################');
            //console.log(rejection);
            if (rejection.config.timeout.isAbortedByUs) {
                // In this case, this error is due to we aborting this request.... so we should not raise error again.
                // return an never-resolving promise as we do not return success or error.
                return $q.defer().promise;
            }

            httpRequestsService.removeRequest(rejection);

            if ((rejection.status === -1) || (rejection.status === 401) || (rejection.status === 408)) {
                setResponseErrorTime(true);
            } else {
                // not targeted error
                setResponseErrorTime(false);
            }

            if (rejection.status === -1) {
                // Check for IE, Firefox, and Chrome.
                //
                // This is due to not reaching the backend server.
                // In this scenarios,
                //      For all browsers, GET request should result in this right away.
                //      For some browser, POST/PUT/DELETE request will result in this right away.
                //      However, for some browser (Firefox, e.g.), POST/PUT/DELETE will be queued (and keep trying) until the timeout time is reached.
                // We need to:
                //      1. In the handler of "Gateway_Not_Reachable",  cancel/abort all pending/queued requests.
                //      2. In uibModal.open or state (GET request with .ng.html url), we set timeout so that we can get response of status = -1.
                if (start_trigger_no_connection) {
                    var to_report = checkResponseErrorReport();
                    if (to_report) {
                        $rootScope.$broadcast("Gateway_Not_Reachable");
                    }
                }
            }

            return $q.reject(rejection);
        }
    };
}]);
