iotgatewayhub.config(function($stateProvider,$urlRouterProvider){

    $stateProvider.state('hub.dashboard.documentation',{
        url: '/dashboard/documentation',
        templateUrl: '/partials/hub.dashboard.documentation.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope){
            $scope.viewSensorSetupTutorial = function () {
                var url = $state.href('tutorial.sensorsetup', {parameter: "parameter"});
                window.open(url,'_blank');
            };
            $scope.viewSensorCloudTutorial = function () {
                var url = $state.href('tutorial.sensorcloud', {parameter: "parameter"});
                window.open(url,'_blank');
            };
            $scope.viewHelixTutorial = function () {
                var url = $state.href('tutorial.helixapp', {parameter: "parameter"});
                window.open(url,'_blank');
            };
            $scope.viewSaveDeployTutorial = function () {
                
                console.log("Is this Quark? " + $scope.isQuark);
                var state_chosen = 'tutorial.savedeploy';
                if ($scope.isQuark) {
                    state_chosen = 'tutorial.savedeploy_quark';
                }

                var url = $state.href(state_chosen, { parameter: "parameter" });
                console.log("URL is " + url);
                window.open(url,'_blank');
            };
        }
    
    });
});