// These are general functions used in several states.
iotgatewayhub.expandHub_gatewayOperations = function($scope,$http,$state,$timeout,$window) {

    $scope.rebootGateway = function(_success, _error, timeout_value){
        // briefly cancel the network checking so that no connection error won't pop-up.
        // When we go to state.hub again, it will be rescheduled.
        $scope.stopCheckingNetwork();

        // We give reboot gateway a timeout, so that we will get some response back.
        $http({
            method: 'PUT',
            url:    '/api/osc',
            timeout: reboot_request_timeout_milliseconds
        }).then(function successCallback(response) {
            $timeout(function () {
                // do not validate session so that we can show login.
                do_validate_session = false;
                //$window.location.href = '/';
                $state.go('welcome.login');
            }, timeout_value);
            if(_.isFunction(_success)){ _success(response); }
        }, function errorCallback(response) {
            $scope.startCheckingNetwork();
            if(_.isFunction(_error)){ _error(response); }
        });
    };

    $scope.scanUSBDevices = function(){
        $scope.scanning_usb = true;
        console.log("hub controller: scanning usb devices");
        $http({
            method: 'GET',
            url:    '/api/usb?nocache=' + new Date().getTime()
        }).then(function successCallback(response) {
            $scope.usb_devices = response.data;
            $scope.scanning_usb = false;
            console.log("hub controller: scanUSBdevices found "+$scope.usb_devices.length+" usb devices");
        },function errorCallback(response) {
            console.log("error scanning usb devices");
            $scope.scanning_usb = true;
        });
    };

    $scope.logout = function(){
        var _method   = "GET";
        var _uri      = '/api/auth?nocache=' + new Date().getTime();

        $http({
            method: _method,
            url:    _uri,
            headers : {'Content-type' : 'application/json'}
        }).then(function successCallback(response) {
            $state.go('welcome.login');
        }, function errorCallback(response) {
            $state.go('welcome.login');
        });
    };

};
