iotgatewayhub.config(function($stateProvider,$urlRouterProvider){

    $stateProvider.state('tutorial',{ // only state that is not a descendant of hub. this presents the login page and background image
        url: '',
        abstract:    true,
        templateUrl: '/partials/hub.tutorial.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,$uibModal){
            setupNoConnection(true);
        }
    })
    .state('tutorial.sensorsetup',{
        url: '/tutorial/sensorsetup',
        templateUrl: '/partials/tutorial.sensorsetup.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope){
            authorization = {};
        }
    })
    .state('tutorial.sensorcloud',{
        url: '/tutorial/sensorcloud',
        templateUrl: '/partials/tutorial.sensorcloud.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope){
            authorization = {};
        }
    })
    .state('tutorial.helixapp',{
        url: '/tutorial/helixapp',
        templateUrl: '/partials/tutorial.helixapp.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope){
            authorization = {};
        }
    })
    .state('tutorial.savedeploy',{
        url: '/tutorial/savedeploy',
        templateUrl: '/partials/tutorial.savedeploy.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope){
            authorization = {};
        }
    })
    .state('tutorial.savedeploy_quark', {
        url: '/tutorial/savedeploy_quark',
        templateUrl: '/partials/tutorial.savedeploy_quark.ng.html?version=' + gui_version,
        controller: function ($window, $scope, $state, $rootScope) {
            authorization = {};
        }
    });
});
