iotgatewayhub.config(function($stateProvider,$urlRouterProvider){

    $stateProvider.state('hub',{ //parent state of all of the logged-in parts of the app. it loads the package library when user logs in
        url: '',
        abstract: true,
        templateUrl: '/partials/hub.ng.html?version=' + gui_version,
        resolve: {
              authenticate: authenticate
        },
        controller: function ($window, $scope, $state, $location, $rootScope, $http, $uibModal, $q, $sce, $timeout, workerProcessRestService, utilityFunctionsService) {
            console.log("hub controller: starting #001");

            setupNoConnection(false);

            $scope.security_enabled   = window.location.protocol === 'https:';

            if (oem_branding_logo_file) {
                $scope.oem_logo_file = oem_branding_logo_file;
                $scope.hideOEMBranding = false;
            } else {
                $scope.hideOEMBranding = true;
            }

            $scope.dashboard        = false;
            $scope.usb_devices      = [];
            $scope.available        = [];
            $scope.installed        = [];
            $scope.package_updates  = [];
            $scope.updates          = 0;
            $scope.sensors          = [];
            $scope.sensor_map       = {};
            $scope.upgrades         = 0;
            $scope.mec              = false;
            $scope.isQuark          = true;

            $scope.humidity     = "";
            $scope.temperature  = "";

            $scope.connected  = false;
            $scope.pro = false;

            $scope.https_port   = https_port;

            $scope.updating_package     = {};
            $scope.updated_package      = {};
            $scope.update_package_failed = {};
            $scope.update_package_fail_message = {};
            $scope.installing_package   = {};
            $scope.uninstalling_package = {};
            $scope.package_activity     = false;
            $scope.starting_package     = {};
            $scope.stopping_package     = {};
            $scope.enabling_package     = {};
            $scope.installed_package    = {};

            $scope.deleting_repository  = {};

            $scope.noderedurl  = $sce.trustAsResourceUrl(window.location.protocol + "//" + window.location.hostname + ":1880");

            $scope.luciurl     = $sce.trustAsResourceUrl("https://" + window.location.hostname + ":443");

            $scope.httpurl     = $sce.trustAsResourceUrl("http://" + window.location.hostname + "");
            
            $scope.httpsurl    = $sce.trustAsResourceUrl("https://" + window.location.hostname + ":3092");
            
            $scope.cloudcmdurl = $sce.trustAsResourceUrl(window.location.protocol + "//" +  window.location.hostname + ":8000");

            $scope.foundOSUpdate = false;
            $scope.foundOSUpdate_Packages = false;
            $scope.OSUpdate_Packages =[];

            $scope.package_refresh    = 0; //if package list is reloaded from server, increment this by one to force update
            $scope.gotPackagesList = true;
            $scope.gotPackagesList_Error = false;


            $scope.setDashboard = function(){
                $scope.dashboard        = true;
            };
            $scope.isDashboard  = function(){
                return $scope.dashboard;
            };
            $scope.hasUpdates = function(){
                return $scope.updates>0;
            };
            $scope.hasOSUpdate = function(){
                // always return true
                return true;
            };

            $scope.updatePackageStatuses   = function(){
                console.log("hub control updating package statuses #720a");

                if($scope.package_activity) return;
                $scope.package_activity = true;

                for(var _i=0,_len=$scope.packages.length;_i<_len;_i++){
                    console.log("hub control updating package statuses #720a1: ");
                    var _package = $scope.packages[_i];
                    console.log("hub control updating package statuses #720a2: "+_package.name);
                    var _service = _package.service;
                    if(_service && _service[0]){

                        var _all_services_running = true;
                        var _all_services_enabled = true;
                        var _num_services         = _service.length;

                        for(var _j=0;_j<_num_services;_j++){
                            var _service_sub   = $scope.service_statuses[_service[_j]].SUB;
                            var _service_state = $scope.service_statuses[_service[_j]].State;
                            console.log("hub control updating package statuses #720a2 "+_package.name+" looking up status of service "+_j+" "+_service[_j]+" SUB="+_service_sub+" State="+_service_state);
                            if(_service_sub  !=='Running'){
                                console.log("setting running to FALSE");
                                _all_services_running = false;
                            }
                            if(_service_state !=='enabled'){
                                console.log("setting autorun to FALSE");
                                _all_services_enabled = false;
                            }
                        }
                        console.log("hub control updating package statuses #720d "+_package.name+" setting statuses as "+_all_services_running+" "+_all_services_enabled );
                        //var _service_status = $scope.service_statuses[_service[0]]; //just one service for now. Must ask Nick what he wants to do for more than one
                        $scope.package_statuses[_package.name] = {
                            'running': _all_services_running ,
                            'autorun': _all_services_enabled };
                        _package.running = _all_services_running; // Directly on package for sorting reasons in the Installed Packages table
                        _package.autorun = _all_services_enabled; // Directly on package for sorting reasons in the Installed Packages table

                        console.log("hub control updating package statuses #720b "+_package.name +
                                  " running: "+$scope.package_statuses[_package.name].Running +
                                  " autorun: "+$scope.package_statuses[_package.name].autorun);
                        //$scope.$apply();

                    }
                }

                $scope.package_activity = false;
            };

            $scope.setArrays = function(_set_services){
                //$scope.featured   = $scope.packages.filter(function(_package){
                  //return !_package.installed && _package.curated;
                //});
                console.log("hub controller: setting available packages #009a");
                // only update list when it is not empty
                if ($scope.packages) {
                    $scope.available  = $scope.packages.filter(function(_package){
                        return !_package.installed;
                    });
                    console.log("hub control setting installed packages #009b");
                    $scope.installed  = $scope.packages.filter(function(_package){
                        return _package.installed;
                    });
                    console.log("hub control setting package_updates #009c");
                    $scope.package_updates = $scope.installed.filter(function(_package){
                        return _package.upgrade_version && _package.upgrade_version !=="";
                    });
                    console.log("hub control setting (os) updates #009d");
                    //$scope.updates = ($scope.hasOSUpdate() ? 1 : 0) + $scope.package_updates.length;
                    $scope.updates = $scope.package_updates.length;

                    $scope.curated =  $scope.available.filter(function(_package){
                        return _package.curated;
                    });

                    if(_set_services){
                       $scope.setServices();
                    }
                }
            };

            $scope.launchQuickTools = function(_toolname) {
                if (_toolname==='nodered') {
                    if (navigator.userAgent.toLowerCase().indexOf('firefox') > -1) {
                        //Firefox has to open this in a new tab
                        var win = window.open($scope.noderedurl, '_blank');
                        win.focus();
                    } else {
                        $state.go("hub.dashboard.nodered");
                        $rootScope.isSensorPanelExpanded = false;
                    }
                    //var _url = "<gateway ip>:1880";
                    //console.log("Launching Node Red in iframe with url "+_url);
                } else if (_toolname==='helix') {
                    //var _url = "TBD";
                    //console.log("Launching Helix in iframe with url "+_url);

                    $state.go("hub.dashboard.helix");
                    $rootScope.isSensorPanelExpanded = false;
                } else if (_toolname==='luci') {
                    //var _url = "https://<gateway ip>";
                    //console.log("Launching LuCl in iframe with url "+_url);

                    var win = window.open($scope.luciurl, '_blank');
                    win.focus();
                } else if(_toolname==='cloudcmd'){
                    if (window.location.protocol==='https:') {
                        var win = window.open($scope.cloudcmdurl, '_blank');
                        win.focus();
                    } else {
                        if (navigator.userAgent.toLowerCase().indexOf('firefox') > -1) {
                            //Firefox has to open this in a new tab
                            var win = window.open($scope.cloudcmdurl, '_blank');
                            win.focus();
                        } else {
                            $state.go("hub.dashboard.cloudcmd");
                            $rootScope.isSensorPanelExpanded = false;
                        }
                    }
                }
            };

            $scope.setupPreventModalClosing = function(){

                var blockUI = false;
                var listener = function(){};
            };

            $scope.initHubTasks = function() {
                // We are doing this at hub load time, so that we can get the available update count to display.
                function initSetPackages() {
                    console.log("hub controller: getting packages #005a");
                    $http({
                        method : 'GET',
                        url    : '/api/packages?nocache=' + new Date().getTime()
                    }).then(
                        function successCallback(response) {
                            $scope.packages = response.data;
                            console.log("hub controller: found packages by API call 005b");
                            // do not need and do not refresh service at this time.
                            $scope.setArrays(false);
                        },function errorCallback(response) {
                            console.log("error getting packages 005E");
                        });
                }

                function initCheckMECDetails(){
                    console.log("hub controller: checking mec status #7223");
                    $http({
                        method: 'GET',
                        url:    '/api/mec?nocache=' + new Date().getTime()
                    }).then(function successCallback(response) {
                        var _mec = response.data;
                        if( _mec.status ==='success'){
                            $scope.mec = {
                                type: _mec.type ? _mec.type : "",
                                installed: _mec.installed ? _mec.installed : "",
                                enabled: _mec.enabled ? _mec.enabled : ""
                            };
                            console.log("hub controller: found mec installed as "+$scope.mec.installed+" #7223b");
                        }else{
                            console.log("errorCallback with HTTP status "+response.status);
                        }
                        initSetPackages();
                    }, function errorCallback(response) {
                        //this only happens for server error
                        console.log("errorCallback with HTTP status "+response.status);
                        initSetPackages();
                    });
                }

                function initScanUSBDevices(){
                    $scope.scanning_usb = true;
                    console.log("hub controller: scanning usb devices");
                    $http({
                        method: 'GET',
                        url:    '/api/usb?nocache=' + new Date().getTime()
                    }).then(function successCallback(response) {
                        $scope.usb_devices = response.data;
                        $scope.scanning_usb = false;
                        console.log("hub controller: scanUSBdevices found "+$scope.usb_devices.length+" usb devices");
                        initCheckMECDetails();
                    },function errorCallback(response) {
                        console.log("error scanning usb devices");
                        $scope.scanning_usb = true;
                        initCheckMECDetails();
                    });
                }

                function initCheckProStatus(){
                    $http({
                        method: 'GET',
                        url:    '/api/pro?nocache=' + new Date().getTime()
                    }).then(function successCallback(response) {
                        if (response.data.result === 'NA') {
                            // This means that the backend cannot get the pro status, so we do not update status.
                        } else {
                            $scope.pro = (response.data.result === 'True') ;
                        }
                        console.log("hub controller: found pro as "+$scope.pro+" #7223c"+response.status);
                        initScanUSBDevices();
                    }, function errorCallback(response) {
                        //this only happens for server error
                        console.log("errorCallback with HTTP status "+response.status);
                        initScanUSBDevices();
                    });
                }

                function initSetDevice(){
                    console.log("hub controller: getting device 006a");
                    $http({
                        method : 'GET',
                        url    : '/api/osc?nocache=' + new Date().getTime()
                    }).then(
                        function successCallback(response) {
                            $scope.device = response.data;

                            // Note: For string, it is indexOf not indexof
                            if ($scope.device.model.indexOf(quark_name_string) <= -1) {
                                $scope.isQuark = false;
                            } else {
                                $scope.isQuark = true;
                            }

                            //get device uptime, ideally use a timestamp on server start from server instead
                            var uptime = $scope.device.uptime.split(' '); // format: 0d 1h 2m
                            var days = uptime[0].substr(0,uptime[0].length-1);
                            var hours = uptime[1].substr(0,uptime[1].length-1);
                            var minutes = uptime[2].substr(0,uptime[2].length-1);
                            var milliseconds = (days*60*60*24*1000)+(hours*60*60*1000)+(minutes*60*1000);
                            var upSince = new Date( new Date() - milliseconds);
                            $scope.deviceUpSince = upSince.getTime();
                            console.log("hub controller: found device by API call 006b");
                            initCheckProStatus();
                        },function errorCallback(response) {
                            console.log("error scanning usb devices");
                            initCheckProStatus();
                        });
                }

                // run this first, so that the GUI can show the gateway info fast enough
                initSetDevice();
            };

            iotgatewayhub.expandHub_proxy($scope,$http,$rootScope,$state,workerProcessRestService,utilityFunctionsService);

            iotgatewayhub.expandHub_services($scope,$http,$q,workerProcessRestService);

            iotgatewayhub.expandHub_save_image($scope,$uibModal,workerProcessRestService);

            iotgatewayhub.expandHub_security($scope,$http,$uibModal,workerProcessRestService);

            iotgatewayhub.expandHub_repositories($scope,$http,$state,$uibModal,workerProcessRestService);

            iotgatewayhub.expandHub_packages($scope,$state,$http,$rootScope,$uibModal,$timeout,workerProcessRestService);

            iotgatewayhub.expandHub_pro_upgrade($scope,$http,$state,$uibModal,$timeout,workerProcessRestService,utilityFunctionsService);

            iotgatewayhub.expandHub_openOSUpdate($scope,$uibModal,$http,workerProcessRestService);

            iotgatewayhub.expandHub_gatewayOperations($scope,$http,$state,$timeout,$window);

            $scope.$watchCollection('package_refresh',function(){ $scope.setArrays(true); });

            $scope.$on('hub.lockModal', function(scope){
                blockUI = true;

                $('.close-admin-modal').hide();

                listener = scope.targetScope.$on('modal.closing', function(event, reason, closed) {
                    //console.log(prefix + 'modal.closing: ' + (closed ? 'close' : 'dismiss') + '(' + reason + ')');
                    if (!blockUI) return;

                    //console.log('\t' + prefix + 'veto (interactive)');
                    event.preventDefault();
                });

            });

            $scope.$on('hub.unlockModal', function turnOffListener(){

                $('.close-admin-modal').show();
                listener();// weird angular-ism
            });

            $rootScope.$on('$locationChangeStart', function(event) {
                if(!$rootScope.accepted_license){
                    $state.go('hub.license',{});
                }
            });

            $scope.setupPreventModalClosing();

            // Note: we cannot start a lot of http request at this point.
            // On slow systems, if there are too many pending requests, new request will be aborted!
            // So for new request, add it into initHubTasks functions after other requests are done.
            $scope.initHubTasks();

            // Other tasks are done when we enter network check state.

            console.log("hub controller: EXITING #999z");
        }
    });
});


