// These are general functions used in several states.
iotgatewayhub.expandHub_proxy = function($scope,$http,$rootScope,$state,workerProcessRestService,utilityFunctionsService) {
    $scope.settings      = {proxy: null };

    $scope.proceedToNetworkCheck = function(){
        $rootScope.loggedin = true;

        utilityFunctionsService.checkNetwork(function(response){

                function goNextState(has_network) {
                    if (has_network) {
                        //network is up
                        $scope.connected = true;
                        if($rootScope.redirectState){
                            $state.go($rootScope.redirectState);
                        }else{
                            $state.go('hub.dashboard.sensors');
                        }
                    } else {
                        //network is down
                        $scope.connected = false;
                        $state.go("hub.nonetwork",{});
                    }
                }

                $scope.startCheckingNetwork();

                var _https_conn = response.data.https_conn; //should this match the window-level protocol? The spec says this value
                var _ok         = _https_conn=="True";

                goNextState(_ok);
            }, function(response){
                //should not occur (network being down is handled in success call with status in json)
                console.log("error callback for test proxy settings");
            });
    };

    $scope.startCheckingNetwork = function(){
        if (periodicCheckNetworkFunc !== null) {
            clearInterval(periodicCheckNetworkFunc);
        }
        periodicCheckNetworkFunc = setInterval(function(){
            utilityFunctionsService.checkNetwork(
                function(response){
                    var _https_conn = response.data.https_conn; //should this match the window-level protocol? The spec says this value
                    var _ok         = _https_conn==="True";
                    var _fail       =  simulate_network_failure && network_index++ % 2 === 0;
                    if(_ok && !_fail){
                        console.log("hub controller: check network found https network connection is UP #9200b1");
                        $scope.connected = true;
                    }else{
                        //network is down
                        console.log("hub controller: check network found https network connection is DOWN #9200b2");
                        $scope.connected = false;
                    }

                    // set pro status
                    if (response.data.pro_status.result === 'NA') {
                        // cannot get the current pro status, skip.
                    } else {
                        $scope.pro = (response.data.pro_status.result === 'True') ;
                    }

                    // update package list
                    if (response.data.package_list === 'NA') {
                        // no package_list update
                    } else {
                        // cast string of array to array
                        $scope.packages = JSON.parse(response.data.package_list);
                        $scope.package_refresh = Number($scope.package_refresh) + 1;
                    }

                    // update repo list
                    if (response.data.repo_list === 'NA') {
                        // no repo_list update
                    } else {
                        // only update when repo_list has an array..
                        // empty array is still an array.
                        if (response.data.repo_list) {
                            $scope.repositories = response.data.repo_list;
                        }
                    }
                },
                function(response){
                    console.log("hub ERROR checking network at interval #9200E " + response.status);
                    // Do not update status if we cannot get any information.
                    // $scope.connected = false;
                });
        },network_check_interval_milliseconds);
    };

    $scope.stopCheckingNetwork = function() {
        if (periodicCheckNetworkFunc !== null) {
            clearInterval(periodicCheckNetworkFunc);
        }
    };

    $scope.getProxySettings = function(resultHandler){
        var tempUrl = '/api/proxy?request=list';
        workerProcessRestService.informGeneralChecking('GET', tempUrl, false, false, null,
            'Network Settings', '', 'Retrieving Settings...',
            function(data){
                // success call back
                $scope.proxy = data;
                $scope.settings.proxy = $scope.proxy;
                $scope.settings.proxy.single_url = "";
                $scope.settings.proxy.single_port = "";

                resultHandler();
            }, rest_check_interval_milliseconds);
    };

    $scope.testProxySettings = function(successCallback, failCallback){
        //returns success or failure. does not send settings in form. uses the ones in database.
        //Nick says button should be enabled only when form is clean.
        console.log("testing proxy settings");
        $scope.networkSettingsTest = {running:true};
        utilityFunctionsService.checkNetwork(
            function(response){
                var _https_conn = response.data.https_conn;
                var _ok         = _https_conn=="True";

                if(_ok){
                    successCallback();
                    // $scope.networkSettingsTest = {running:true,status:'success'};
                    //network is up
                    //$state.go("hub.dashboard.sensors",{});
                }else{
                    failCallback({success:false, message:"Proxy settings invalid ["+_https_conn+"]"});
                    //$scope.networkSettingsTest = {running:true,status:'fail'};
                    //network is down
                    //$state.go("hub.nonetwork",{});
                }
                console.log("Proxy test returned: "+_https_conn);
            },
            function(response){
                failCallback();
                //should not occur (network being down is handled in success call with status in json)
                console.log("error callback for test proxy settings");
            }
        );
    };

    $scope.applyProxySettings = function(successCallback, failCallback){
        //save proxy settings to backend

        var _single_url  = $scope.settings.proxy.single_url;
        var _single_port = $scope.settings.proxy.single_port;

        var _http_url    = $scope.settings.proxy.http_url;
        var _http_port   = $scope.settings.proxy.http_port;

        var _https_url   = $scope.settings.proxy.https_url;
        var _https_port  = $scope.settings.proxy.https_port;

        var _ftp_url     = $scope.settings.proxy.ftp_url;
        var _ftp_port    = $scope.settings.proxy.ftp_port;

        var _socks_url   = $scope.settings.proxy.socks_url;
        var _socks_port  = $scope.settings.proxy.socks_port;

        var _no_proxy    = $scope.settings.proxy.no_proxy;

        var tempURL = '/api/proxy'+
            "?http_url="+encodeURIComponent(_http_url)+
            "&http_port="+encodeURIComponent(_http_port)+
            "&https_url="+encodeURIComponent(_https_url)+
            "&https_port="+encodeURIComponent(_https_port)+
            "&ftp_url="+encodeURIComponent(_ftp_url)+
            "&ftp_port="+encodeURIComponent(_ftp_port)+
            "&socks_url="+encodeURIComponent(_socks_url)+
            "&socks_port="+encodeURIComponent(_socks_port)+
            "&no_proxy="+encodeURIComponent(_no_proxy);

        workerProcessRestService.performGeneralExecution('POST', tempURL, false, false, null,
            function(data) {
                var _https_conn = data.https_conn;
                if (_https_conn==="True") {
                    $scope.connected = true;
                    successCallback({success: true, message: "Applied proxy settings"});
                } else {
                    $scope.connected = false;
                    failCallback({success: false,   message: "Applied proxy settings, but https connection does not work under the settings. Some features will be disabled."});
                }
            }, function(errorMessage) {
                failCallback({success: false, message: errorMessage});
            }, rest_check_interval_milliseconds_m);
    };
};
