iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    $stateProvider.state('hub.dashboard.tools',{
        url: '/dashboard/tools',
        templateUrl: '/partials/hub.dashboard.admin.ng.html?version=' + gui_version,
        controller: function($scope,$state,$rootScope,$uibModal){
            console.log("admin tools mec status "+$scope.mec+" #0812a");

            $scope.confirmRestartOS = function(){
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.confirm.restart.os.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope,$state,$http,$rootScope,$timeout){
                        $scope.restarting_os     = false;
                        $scope.restarted_os      = false;
                        $scope.message           = '';
                        $scope.error_message     = '';

                        $scope.restartOS = function(){
                            if($scope.restarting_os){
                                return;
                            }
                            $scope.error_message = '';
                            $scope.restarting_os = true;
                            $scope.restarted_os  = false;
                            $scope.rebootGateway(
                                function successCallback(response) {
                                    //no defined return json for this. if 200 OK then assume success of call (according to Nick)
                                    console.log("reboot gateway process was successful  #1871b");
                                    $scope.restarting_os = false;
                                    $scope.restarted_os  = true;
                                    $scope.message       = "The reboot process was successful. Forwarding to login in 10 seconds...";
                                },function errorCallback(response) {
                                    $scope.error_message = 'Rebooting gateway failed! Please manually reboot the gateway!';
                                    console.log("reboot gateway  process failed  #1871E");
                                }, restart_welcome_latency_milliseconds);
                        };

                        $scope.closeModal = function(){
                            console.log("close confirm restart modal");
                            modalInstance.dismiss('cancel');
                        };
                    }
                });

            };

            $scope.configureNetwork = function(){
                $scope.getProxySettings(function(){
                    $scope.cancelText="Cancel";
                    $scope.isInsideModal = true;
                    $scope.checkMECDetails();

                    $scope.skipSettings = function(){
                        networkSettingsModal.dismiss();
                    };

                    $scope.doneSuccessCallback = function(){
                        networkSettingsModal.dismiss();
                    };

                    console.log("show network settings from dashboard");
                    var networkSettingsModal = $uibModal.open({
                        templateUrl: '/partials/hub.networksettings.dashboard.ng.html?version=' + gui_version,
                        animation: true,
                        size: 'lg',
                        scope: $scope,
                        backdrop: modal_light_dismiss_enabled
                    });

                    $scope.closeModal = function(){
                        console.log("hub canceling os upgrade #9033b");
                        networkSettingsModal.dismiss('cancel');
                    };
                });
            };

            $scope.onClickUpgrade = function(){
                var suppress = $scope.suppressReminder = getCookie('upgradeMessage')!="";

                if($scope.mec.enabled){
                    console.log("no upsell, suppress and mec enabled");
                    // no upsell, suppress and mec
                    $scope.openOSUpgradeModal1b();
                    return;
                }

                console.log("no upsell, suppress and no mec");
                // no upsell, suppress, and no mec
                $scope.openOSUpgradeModal2();
            };

            $scope.onClickSaveOSImage = function(){
               
                var suppress = $scope.suppressReminder = getCookie('upgradeMessage')!="";
              
                if(!$scope.pro && !suppress){
                    console.log("upsell, no suppress");
                    // upsell, no suppress
                    $scope.confirmOSUpgrade($scope.openOSImageModal0);
                    return;
                }
               
                //console.log("no upsell, suppress and no mec");
                // no upsell, suppress, and no mec
                $scope.openOSImageModal0();
            };

            $scope.openChangePassModal0 = function(){

                console.log("hub control opening change password modal #448a");
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.change.password.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope,$state,$http){
                        console.log("hub control opening OS image modal 0 #448b");
                        $scope.error_message = null;

                        $scope.update = {
                            username: null,
                            password: null,
                            newpassword: null,
                            confirmnewpassword: null
                        };
                        $scope.closeModal = function(){
                            console.log("hub control closing os image modal 0 #448c");
                            modalInstance.dismiss('cancel');
                        };

                        $scope.changePassword = function(){
                            $scope.error_message = null; //reset so error message goes away
                            console.log("hub changing password #2029a");
                            var _username           = $scope.update.username;
                            var _password           = $scope.update.password;
                            var _newpassword        = $scope.update.newpassword;
                            var _confirmnewpassword = $scope.update.confirmnewpassword;
                            if(!_newpassword || _newpassword.length<3){

                                $scope.error_message = "The new password must be at least 3 characters long.";
                            }else if(_newpassword!==_confirmnewpassword){
                                $scope.error_message = "The new passwords do not match.";
                            }else{

                                $http({
                                    method: 'PUT',
                                    url:    '/api/auth',
                                    data:  JSON.stringify({"username":_username,"password":_password,"newpassword":_newpassword}),
                                    headers : {
                                        'Content-type' : 'application/json'
                                    }
                                }).then(function successCallback(response) {

                                    if(response.data.status==='success'){
                                        console.log("hub change password succeded #2029b");
                                        $scope.openConfirmChangePasswordModal(_username);
                                        modalInstance.dismiss('cancel');
                                    }else{
                                        console.log("hub change password failed #2029F");
                                        $scope.error_message = "ERROR: "+response.data.error;
                                        //what to do if fails?
                                    }
                                }, function errorCallback(response) {

                                    console.log("hub change password failed #2029F");
                                    $scope.error_message = "ERROR: "+response;

                                });

                            }

                        };

                    }

                });
            };

            $scope.openConfirmChangePasswordModal = function(username){

                console.log("hub control opening confirm change password modal #449a");
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.change.password.confirm.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope){
                        console.log("hub control opening confirm change password modal #449b", username);
                        $scope.username = username;
                        $scope.closeModal = function(){
                            console.log("hub control closing os image modal 0 #448c");
                            modalInstance.dismiss('cancel');
                        };



                    }

                });
            };

            $scope.showFactoryReset = function(){
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.inform.os.update.inprogress.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope,$state,$http,$rootScope,$timeout){
                        $scope.title_message = "Restore Factory OS: Restoring Default Operating System";
                        $scope.description_message = "The gateway's original operating system and file system is being restored. This should take several minutes.";
                        $scope.bar_before_message = "Restoring Default OS...";
                        $scope.bar_after_message = "The gateway will automatically restart once restore is complete.";
                        $scope.error_message = null;
                        $scope.show_close_button = false;

                        $scope.closeModal = function(){
                            modalInstance.dismiss('cancel');
                        };

                        $scope.doFactoryReset = function(){
                            console.log("hub control peforming factory reset #6019a");
                            //$scope.doing_factory_reset = true;
                            $scope.error_message = null;
                            $scope.show_close_button  = false;

                            // briefly cancel the network checking so that no connection error won't pop-up.
                            // When we go to state.hub again, it will be rescheduled.
                            $scope.stopCheckingNetwork();

                            $http({
                                method: 'POST',
                                url:    '/api/osc'
                            }).then(function successCallback(response) {
                                console.log("hub control factory reset returned status: "+response.data.status+". Forwarding to login. #6019b");
                                $timeout(function(){
                                    // force to show login
                                    do_validate_session = false;
                                    $state.go("welcome.login");
                                }, factory_reset_welcome_latency_milliseconds);
                            },function errorCallback(response) {
                                console.log("hub control factory reset process encountered server error #6019z. Forwarding in 30 seconds to login.");
                                $scope.startCheckingNetwork();
                                $scope.bar_after_message = null;
                                $scope.error_message = 'Restoring failed!';
                                $scope.show_close_button = true;
                                setupNoConnection(true);
                            });
                        };

                        setupNoConnection(false);
                        $scope.doFactoryReset();
                    }
                });
            };

            $scope.confirmFactoryReset = function(){
                console.log("confirming factory reset");
                var modalInstance = $uibModal.open({
                    templateUrl: 'partials/hub.confirm.factory.reset.ng.html?version=' + gui_version,
                    animation:    true,
                    size: 'lg',
                    backdrop: modal_light_dismiss_enabled,
                    scope: $scope,
                    controller: function($scope){

                        $scope.cancelFactoryReset = function(){
                            console.log("canceling factory reset");
                            modalInstance.dismiss('cancel');
                        };

                        $scope.doFactoryReset = function(){
                            $scope.showFactoryReset();
                            modalInstance.dismiss('cancel');
                        };
                    }
                });
            };
        }
    });
});
