iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state("hub.test",{
        url:         '/hub/test', 
        templateUrl: '/partials/hub.test.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope){
            if(!bypass_auth && !authorization.username){
               $state.go('welcome.login',{}); 
            }
        }
    });
});