iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('welcome.login',{ 
        url:         '/welcome/login', 
        templateUrl: '/partials/welcome.login.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,$timeout){
            authorization = {};
            // hide error messages until 'submit' event
            $scope.submitted = false;
            // hide success message
            $scope.showMessage = false;
            // show go button
            $scope.showGo = true;
            // status message
            $scope.barMessage = 'Authenticating...';
            // server error message
            $scope.serverErrorMessage = 'A server error occurred!';
            // method called from shakeThat directive
            $scope.keyPressed = function($event) {
                if($event.keyCode != 13) return;
                if ($scope.login.username && $scope.login.password) $scope.login();
            };

            $scope.login = function() {
                // hide go button and show status bar
                $scope.showGo = false;

                var internalAuthentication = function(firstTime) {
                    $scope.authentication = { "username": $scope.login.username, "password": $scope.login.password };
                    var _username = $scope.authentication.username;
                    var _password = $scope.authentication.password;

                    var do_auth = 'False';
                    if (firstTime) {
                        do_auth = 'True';
                    }

                    $http({
                        method: 'POST',
                        url: '/api/auth',
                        data:  JSON.stringify({"username":_username,"password":_password, "do_auth": do_auth}),
                        headers : {
                            'Content-type' : 'application/json'
                        }
                    }).then(function successCallback(response) {
                        //check result of login, set username in session if success
                        //should not succeed for HTTP DIGEST on first call (since no Authorization header provided)
                        //can be used for old style simple login
                        if (response.data.status === 'success') {
                            if (response.data.init_in_progress === false) {
                                ////////////////////////////////
                                // Authentication worked, and server initialization is done.
                                ////////////////////////////////
                                // done
                                $scope.showGo = true;
                                $scope.handleAuthenticated();
                            } else {
                                ////////////////////////////////
                                // Authentication worked, and server initialization is not done.
                                ////////////////////////////////
                                // start checking after some delay (larger delay than the authentication part)
                                setTimeout(function() {
                                        internalAuthentication(false);
                                    }, rest_check_interval_milliseconds_m);
                                $scope.barMessage = 'Waiting for server setup...';
                            }
                        } else {
                            ////////////////////////////////
                            // Authentication call went through, but authentication failed.
                            ////////////////////////////////
                            $scope.broadcastFailure("welcome.loginFailed");
                        }
                    }, function errorCallback(response) {
                        ////////////////////////////////
                        // Authentication call failed
                        ////////////////////////////////
                        if (response.status == 401) {
                            $scope.broadcastFailure("welcome.loginFailed");
                        } else if (response.status === 408) {
                            // request timeout
                            // ignore and continue checking
                            // start checking after some delay (larger delay than the init authentication part)
                            setTimeout(function() {
                                        internalAuthentication(false);
                                    }, rest_check_interval_milliseconds_m);
                            // do not change message.
                        } else if (response.status === -1) {
                            $scope.serverErrorMessage = 'Cannot connect to the gateway yet!';
                            $scope.broadcastFailure("welcome.loginServerError");
                            console.log("ERROR response status="+response.status);
                        } else {
                            $scope.serverErrorMessage = 'A server error occurred! http status = ' + response.status;
                            $scope.broadcastFailure("welcome.loginServerError");
                            console.log("ERROR response status="+response.status);
                        }
                    });
                };

                internalAuthentication(true);
            };

            $scope.submit = function() {
                // show success message
                $scope.showMessage = true;
            };
            
            $scope.$on("welcome.loginFailed", function(){
                // show go
                $scope.showGo = true;

                $scope.showAuthenticationError = true;

                var $animate = $('.container div div');
                $animate.addClass( 'shake');
                setTimeout(function() {
                    $animate.removeClass('shake');
                }, 100);

                $scope.login.password=null;

                $animate.addClass("invalid");

                $animate.find('input').on("click", function(){
                    $animate.removeClass("invalid");
                    $scope.showAuthenticationError = false;
                });
            });

            $scope.$on("welcome.loginServerError", function(){
                // show go
                $scope.showGo = true;

                $scope.showServerError = true;

                $timeout(function(){
                    $scope.showServerError = false;
                },8000);

                $('.login-form').find('input').on("click",
                    function() {
                        $scope.$apply(function () {
                            $scope.showServerError = false;
                        });
                    }
                );
            });
        }
    });
});
